.new__details{

  .content_actu__detail {
	font-size: 14px;
	line-height: 1.5;

	.share_social {
	  right: 20px !important;
	  width: auto;

	  @media screen and (min-width: $tablet-medium){
		right: 39% !important;
	  }

	  span {
		margin-right: 10px;
	  }
	}
  }

  .slider{
	&__body{
	  > div{
		background: rgba($maincolor, .6);
	  }
	}
  }
  .bg-red{
	background: $color-4;
	@include clearfix();
	.content__detail{
	  &--main{
		position: relative;
		padding-bottom: 80px;
		.pdf{
		  margin-bottom: 0;
		  text-align: right;
		  margin-top: 45px;
		  a{
			color:$color-42;
			font:{
			  family: $font-1;
			}
			text:{
			  decoration: underline;
			}
		  }
		}
		.share_social{
		  right:0;
		  bottom:0;
		}
		@media screen and (max-width: $tablet-medium){
		  width:100% !important;
		  float:auto !important;
		  margin-bottom: 15px;
		}
	  }
	  .video{
		width:100%;
	  	@media screen and (min-width: $tablet-medium){
		  width:50%;
		  float:left;
		  padding-left:50px;
		}
	  }
	}
  }
  .event{
	@include clearfix();
	@media screen and (min-width: $tablet-medium) {
	  display: flex;
	}
	&__box{
	  @media screen and (min-width: $tablet-medium){
		float:left;
		padding:0;
		margin:0;
		&:first-child{
		  height:auto !important;
		}
	  }
	  @media screen and (max-width: $tablet-medium){
		width:100% !important;
		padding:0;
	  }
	  &:first-child{
		@include bg-gradient(left, $maincolor, $color-22);
		position:relative;
		height:300px;
	  }
	  &--main{
		width: 260px;
		position:absolute;
		top:50%;
		left:50%;
		@include transform(translate(-50%, -50%));
		color:$white;
		h2, .icon_event{
		  font-size: 25px;
		  margin-bottom: 20px;
		  padding-left:0;
		  text: {
			transform: uppercase;
			align: center;
		  }
		  i{
			font: {
			  size: 60px;
			}
		  }
		}
		p{
		  position: relative;
		  padding-left:32px;
		  span:first-child{
			position: absolute;
			left:0;
			top:-5px;
			  color: $color-41;
			  font-size: 18px;
		  }
		  font: {
		  	size: 13px;
		  }
		}
	  }
	  #map-canvass{
		min-height: 400px;
		img{
		  display: block;
		}
	  }
	}
	&+.content__actus{
	  width:100%;
	  .list_items{
		@media screen and (min-width: $tablet-medium){
		  width: 80%;
		  margin:0 auto;
		}
	  }
	}
  }
  .bg-gradient-red{
	@include bg-gradient(left, $color-37, $color-38);
	.slider__card{
	  @media screen and (min-width:$desktop-medium-l){
		max-width: 90%;
	  }
	}
  }
  &__left{
	@media screen and (min-width: $tablet-medium){
	  padding:50px;
	}
	padding:15px;
	h1{
	  font-size: 30px;
	  text-align: left;
	  margin-bottom: 10px;
	}
	.content_produits__detail{
	  text-align: left;
	  p{
		margin-bottom: 30px;
	  }
	  .date-publish{
		margin-bottom:50px;
	  }
	  iframe{
		float:right;
		width:100% !important;
		height:400px;
	  }
	}
	&>*{
	  color: $color-42;
	}
  }
}

.content__detail {

  &--main {

	padding-bottom: 140px;

	.col-12 {
	  margin-bottom: 30px;
	}

	.download_presse {

	  text-align: center;
	  position: relative;
	  right: 0;
	  bottom: 0;
	  font-weight: bold;

	  @media screen and (min-width: $tablet-medium){
		position: absolute;
		right: 40%;
		bottom: 75px;
	  }

	  a {
		text-decoration: underline;
		color: $textcolor;
	  }
	}

	p {
	  margin: 0;
	}

	.block-left {
	  margin-top: 15px;
	  width: 100%;

	  @media screen and (min-width: $tablet-medium){
		float: left;
		max-width: 65%;
		padding-right: 70px;
	  }

	  &:after{
		content: '';
		display: block;
		clear: both;
	  }
	}
	img{
	  margin:20px 40px;
	  max-width: 100%;
	  height: auto !important;
	  @media screen and (max-width: $mobile-small){
		margin:20px 0;
	  }
	  &.alignright {
		float: right;
	  }

	  &.alignleft {
		float: left;
	  }
	}
	iframe {
	  margin-left:40px;
	  margin-top: 20px;
	  height:none;
	  @media screen and (min-width: $tablet-medium){
		float: right;
	  }
	  &:after{
		content: '';
		display: block;
		clear: both;
	  }
	}
  }
}