.page__recette {

  h2 { font-size: 36px; font-family: $font-2; @media screen and (max-width: 768px){ font-size: 24px; } }

  .single-banner { text-align: center; line-height: 0; min-height: 500px;
    @media screen and (max-width: 768px){ height: 260px; min-height: auto; background-position: 25% 20% !important; }
    img{
      display: none;
      @media screen and (max-width: $mobile-small){
        display: block;
      }
    }
    @media screen and (max-width: $mobile-small){
      background: none !important;
      height:auto;
    }
  }
}
#raviolis-au {

  .lable-banner {
    background: #FF9501; /* For browsers that do not support gradients */
    background: -webkit-linear-gradient(left, #FF9501 , #E96815); /* For Safari 5.1 to 6.0 */
    background: -o-linear-gradient(right, #FF9501, #E96815); /* For Opera 11.1 to 12.0 */
    background: -moz-linear-gradient(right,#FF9501, #E96815); /* For Firefox 3.6 to 15 */
    background: linear-gradient(to right,#FF9501 , #E96815); /* Standard syntax (must be last) */

    h2 { color:$white;  padding: 31px 0;  margin: 0;  }
    h1{color:$white;  padding: 31px 0;  margin: 0; font-size: 36px;}
  }

  .row-col-equal { display: table;

    .col-equal {  display: table-cell;  width: 50%;  position: relative;

      @media screen and (max-width: $tablet ) {  display: block;  width: 100%;  }
      @media screen and (max-width: $mobile ) { padding: 20px 15px;  }

      h2 {  margin-bottom: 30px;     margin-top: -8px; }
    }

    &.content_are__raviolis {
      @media screen and (min-width: 1366px ) { max-width: 1360px;  margin: 0 auto; }
    }
  }



  .raviolis-au-left {  color: $color-3; background:  $color-16; padding: 50px 120px;
     a { font-weight: bold; color: $white;  text-decoration: underline; }

    h3 { font-size: 23.4px; font-family: $font-2; padding-top: 8px; margin-bottom: 10px;
      &.top_h3 {    padding-top: 20px; }
      @media screen and (max-width: 768px){ font-size: 20px; }
    }

    ul { list-style: none;

      li {
        font-size: 13px; font-family: $font-3;text-align: left; line-height: 20px;  padding: 0 22px;
      }
    }

    &:after {
      content: '';
      position: absolute;
      left: -50%;
      width: 100vw;
      height: 100%;
      background: $color-16;
      top: 0;
      z-index: -1;
      margin-left: -50vw;
    }

  }

  .min-number { font-size: 12px; position: absolute; right: 12px;  top:54px; font-weight: 700; color: $color-3;

    i { font-size: 17px; }
  }

  .raviolis-au-right { background: $color-3; color: $color-11; padding: 55px 60px;

  .min-number{ left: 6px; color: $color-16;

    i {  padding-right: 9px; font-size: 16px;  }
  }

    h2 { color: $color-16; margin-bottom: 35px !important; }

    .inter { margin-bottom: 18px; display: table;

      span {  border: 2px solid $color-16;  color: $color-6;  border-radius: 50%;  text-align: center;  margin-right: 17px;
        width: 20px;  height: 20px;  display: inline-block;  font-size: 13px;  line-height: 17px;  vertical-align: middle;
      }

      p { display: table-cell; font-size: 13px; text-align: left;  color: $color-17; line-height: 20px;

        b { letter-spacing: 1px; }

        a {  color: $color-17;  text-decoration: underline; font-weight: 700; }
      }

      &.last { margin-bottom: 33px;  }
    }


    .telecharger-text { color: $color-16; font-size: 13px; font-family: $font-6; text-decoration: underline;
      i { font-size: 17px;  margin-right: 10px; }
    }

    &:after {
      content: '';
      position: absolute;
      right: -50%;
      width: 100vw;
      height: 100%;
      background: $color-3;
      top: 0;
      z-index: -1;
      margin-right: -50vw;
    }
  }
}
