/* Search
----------------------------------------------------------------------------------------------------*/
.search {

  &__container{
    display: block;

    @media screen and (min-width: $desktop-small) {
      display: none;
    }
  }

}

.list__search {
  .wrapper {
    margin-top: 30px ;
    margin-bottom: 30px ;
  }

  .search__result {
    margin-bottom: 20px;

    h4 {
      font-size: 16px;
      margin-bottom: 0;
    }

    .entry {
      font-size: 13px;
    }
  }
}