.store {
  &__top {
    @media screen and (min-width: $tablet-medium) {
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-align-content: stretch; /* Safari 7.0+ */
      align-content: stretch;
    }
    .col-4 {
      background: $color-31;
      min-height: 300px;
      color: $white;
      position: relative;
      margin-bottom: 0;
      .box {
        padding: 0 15px;
        @media screen and (min-width: $tablet) {
          padding: 0 15px 40px;
        }
        h1 {
          text-transform: uppercase;
          margin-bottom: 17px;
          font-family: $font-2;
          font-size: 25px;
          margin-top: 20px;
          text-transform: inherit;
          @media screen and (max-width: $mobile){
            padding-top: 20px;
            margin-top: 0;
          }
        }
        &__content {
          height: 170px;
          overflow-y: auto;
          .mCSB_scrollTools .mCSB_draggerRail { width: 5px; background: #ff2254 !important; border-radius: 0px; }
          .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar { background: $white !important; width: 5px; border-radius: 0px; }
        }
        p {
          margin-bottom: 0;
          font-size: 13px;
          text-transform: inherit;
          font-family: $font-1;
        }
        @media screen and (min-width: $tablet) {
          &__content {
            height: 250px;
          }
          h1 {
            font-size: 37px;
            text-align: center;
            margin-top: 48px;
          }
        }
      }
    }
    .col-8 {
      padding: 0;
      margin-bottom: 0;
      background-size: cover !important;
      background-repeat: no-repeat !important;
      position: relative;
      .image {
        background-size: cover !important;
        background-repeat: no-repeat !important;
        height: 100%;
        position: relative;
        background-position: center center !important;
        h2 {
          position: absolute;
          top: 50%;
          transform: translate(-50%, -50%);
          color: $white;
          text-align: center;
          display: inline-block;
          left: 50%;
          background: rgba($maincolor, 0.6);
          padding: 15px 30px;
          text-transform: uppercase;
          @media screen and (max-width: $mobile) {
            font-size: 25px;
          }
        }
      }
      img {
        max-width: 100%;
        max-height: none;
        margin: 0 auto;
        display: block;
        visibility: hidden;
        height: auto;
        @media screen and (min-width: $tablet-medium) {
          max-height: 452px;
          height: 100%;
        }
      }
    }
  }
  &__time {
    ul {
      @media screen and (min-width: $tablet-medium) {
        @include flex();
        justify-content: space-between;
      }
      list-style: none;
      margin-bottom: 0;
      li {
        text-align: center;
        flex: 1;
        padding: 20px 10px;
        p {
          margin-bottom: 0;
          &.day {
            text-transform: capitalize;
            font-family: $font-4;
            color: $color-34;
          }
          &.hours {
            font-family: $font-2;
            text-transform: uppercase;
            font-size: 20px;
            color: $color-30;
            span {
              color: $color-9;
            }
          }
        }
        @media screen and (min-width: $desktop-medium) {
          padding: 35px 10px 27px;
          p {
            &.hours {
              font-size: 25px;
            }
          }
        }
      }
    }
  }
  &__bottom {
    @extend .store__top;
    .col-4 {
      background: $color-33;
      .box {
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        @include transform(translateY(-50%));
        p {
          font-size: 25px;
          text-align: center;
          text-transform: uppercase;
        }
      }
      @media screen and (max-width: 320px){
        min-height: 320px;
      }
      @media screen and (min-width: $tablet-medium) {
        min-height: 455px;
        .box {
          p {
            font-size: 30px;
          }
        }
      }
    }
    .col-8 {
      height: 300px;
      @media screen and (min-width: $tablet-medium) {
        height: auto;
      }
      #map-stores {
        width: 100%;
        height: 455px;
        img {
          visibility: visible;
        }
        .infoBox {
          & > img {
            display: none;
          }
          .infoWindow {
            width: 275px;
            padding: 10px 10px !important;
            border-radius: 0 !important;
            h3 {
              background: $transparent !important;
              color: $color-26 !important;
              font-family: $font-2;
              font-size: 15px !important;
              text-transform: uppercase;
              margin-bottom: 0;
            }
            &.service3 {
              h3 {
                color: $color-9 !important;
              }
            }
            .infowindowContent {
              padding-top: 5px !important;
              .info-addr {
                width: 100% !important;
                .address {
                  font-family: $font-3;
                  font-size: 12px !important;
                  padding-left: 28px;
                  span {
                    font-family: $font-7;
                    margin-left: -28px;
                    margin-right: 10px;
                    &:before {
                      content: '\e80c';
                      font-size: 16px;
                      color: $color-25;
                    }
                  }
                }
              }
            }
          }
          .arrow-down {
            margin-left: 85px !important;
          }
        }
      }
    }
    .col-4 {
      .box {
        text-align: center;
        a {
          text-align: center;
          padding: 10px 15px;
          border: 1px solid $maincolor;
          display: inline-block;
          &:hover {
            color: $white;
            background: $maincolor;
          }
          & + p {
            margin-top: 10px;
          }
        }
      }
    }
  }
  &__more {
    @media screen and (min-width: $tablet-medium) {
      display: flex;
    }
    .item {
      padding: 20px 15px;
      width: 100%;
      float: left;
      @media screen and (min-width: $tablet-medium) {
        width: 33.333%;
      }
      &:not(:nth-child(2)) {
        h2 {
          color: $white;
        }
      }
      h2 {
        text-align: center;
        font-size: 25px;
        margin-bottom: 10px;
        text-transform: uppercase;
        @media screen and (min-width: $tablet-medium) {
          font: {
            size: 30px;
          }
        }
      }
      .content {
        @media screen and (min-width: $tablet-medium) {
          width: 80%;
        }
        @media screen and (min-width: $mobile-small) {
          width: 100%;
        }
        width: 100%;
        margin: 0 auto;
        ul {
          list-style: none;
        }
      }
    }
  }
  .autres {
    .news {
      .slider {
        max-width: 90%;
        .card__item {
          h3 {
            color: $white;
            word-break: break-word;
            text: {
              transform: uppercase;
              align: center;
            }
            font: {
              size: 25px;
            }
          }
        }
      }
    }
  }
}
.box__content::-webkit-scrollbar-track
{
    -webkit-box-shadow: inset 0 0 6px #ff2254;
    background-color: #F5F5F5;
     border-radius: 10px;
}

.box__content::-webkit-scrollbar
{
    width: 5px;
    background-color: #ff2254;
    border-radius: 10px;
}

.box__content::-webkit-scrollbar-thumb
{
    border-radius: 10px;
    background-color: #ff2254;
}