
.contact__form{
  @media screen and (min-width: $tablet-medium){
    display: table;
    width: 100%;
  }

  .wpcf7-not-valid-tip {
    color: $white !important;
    font-size: 13px !important;
  }

  div.wpcf7-mail-sent-ok {
    border: none !important;
  }
  &__view {
    padding: 30px 15px;
    text-align: center;
    width: 100%;
    display: block;
    @media screen and (min-width: $desktop-medium){
      padding: 46px 148px 61px;
    }

    @media screen and (min-width: $tablet-medium) and (max-width: $desktop-medium){
      padding: 46px 50px 61px;
    }

    @media screen and (min-width: $tablet-medium){
      width: 67.667%;
      display: table-cell;
    }
    .fields {
      padding: 5px 10px;
      @media screen and (min-width: $tablet-medium){
        padding: 10px;
      }

    }

    form {
      margin-left: -10px;
      margin-right: -10px;
      .row{
        margin:0;
      }
      p {
        font-size: 13px;
        padding-top: 2px;
        margin-bottom: 18px;
      }

      .haft {
        width: 100%;


        @media screen and (min-width: $tablet-medium){
          float: left;
          width: 50%
        }
      }

      input,select {
        height: 52px;
      }

      input[type="text"], input[type="email"],select,textarea {
        background: $color-35;
        color: $white;
        border: none;
        width: 100%;
        font-size: 13px;
        font-style: italic;
        padding-left: 20px;

        @include placeholder {
          color: $white;
          opacity: 1;
        }
      }

      select {
        margin: 0px;
        background-image: url(../img/icon-down-white.png);
        background-repeat: no-repeat;
        background-position: 96% 50%;
        -webkit-appearance: none;
        appearance: none;
        -moz-appearance: none;
        cursor: pointer;

        &::-ms-expand {
          display: none;
        }

      }
      textarea {
        min-height: 134px ;
        padding-top: 18px;
      }
      .recaptcha{
        &>div{
          margin:0 auto;
        }
      }
      input[type="submit"] {
        background: $color-36;
        border: 2px solid $white;
        color: $white;
        font-family: $font-1;
        text-transform: uppercase;
        padding: 18px 70px;
        height: 50px;
        margin-top: 20px;
        font-size: 13px;
        @include transition(all 0.3s ease-in-out);

        &:hover {
          background: #fff;
          color: $color-7;
          border: 2px solid $white;
        }
      }
    }


  }
  .contact_right {
    min-height: 300px;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: block;
    @media screen and (min-width: $tablet-medium){
      width: 33%;
      display: table-cell;
    }
  }
}
