.form{
  &__cv{
    position: fixed;
    width: 100%;
    top: 0;
    background: rgba(0, 0, 0, 0.6);
    padding: 40px 80px;
    height:100%;
    opacity: 0;
    z-index: -1;
    @media screen and (max-width: $desktop-large){
      padding: 40px 15px;
    }
    &.active{
       opacity: 1;
       z-index: 1000;
      transition: all .5s;
      .form__cv--main{
        transform: translateY(0);
      }
    }
    &--main{
      background: #fff;
      padding: 20px 0;
      width: 80%;
      margin: 0 auto;
      position:relative;
      transform: translateY(-100%);
      transition: all .5s;
      @media screen and (max-width: $desktop-large){
        width: 100%;
      }
      @media screen and (max-width: $desktop-large){
        width: 90%;
        margin:0 auto;
      }
      @media screen and (max-width: $mobile){
        width: 100%;
      }
      @media screen and (max-width: $mobile-small){
        padding:30px 0 20px;
      }
      h2{
        text-align: center;
        text-transform: uppercase;
        font-size: 36px;
        margin-bottom: 20px;
        @media screen and (max-width: $tablet){
          font-size: 30px;
          margin:0 auto 20px;
          width: 90%;
        }
        @media screen and (max-width: $mobile){
          font-size: 25px;
        }
        @media screen and (max-width: $mobile-small){
          font-size: 22px;
        }
      }
      .job-manager-application-form{
        .mCSB_container{
          @media screen and (max-width: $mobile){
            margin:0 !important;
          }
        }
        .mCSB_scrollTools{
          @media screen and (max-width: $mobile) {
            opacity: 0 !important;
          }
        }
        padding:0 20px;
        @media screen and (max-width: $desktop-large){
          height: 500px;
          width: 100%;
        }
        @media screen and (max-width: $mobile){
          height:350px;
        }
        .row{
          .col-6{
            @media screen and (max-width: $tablet){
              width: 100%;
            }
          }
        }
        .message-box{
          textarea{
            @media screen and (max-width: $tablet){
              height:200px;
            }
          }
        }
        .fieldset-upload {
          label {
            width: 100%;
            @media screen and (min-width: $tablet-medium) {
              width: 205px;
            }
            font-size: 14px;
          }
          div.field {
            @media screen and (min-width: $tablet-medium) {
              width: calc(100% - 205px);
            }
            width: 100%;
            &>p {
              span {
                display: block;
                width: 100%;
                clear: both;
              }
            }
          }
        }
        .g-recaptcha{
          @media screen and (max-width: $mobile-small){
            transform: scale(.9);
          }
        }
      }
    }
    &--close{
      position: absolute;
      top: 20px;
      right: 30px;
      width: 25px;
      cursor: pointer;
      @media screen and (max-width: $desktop-medium){
        right:10px;
      }
      @media screen and (max-width: $mobile-small){
        top:10px;
      }
    }
  }
}