/* Animations view
----------------------------------------------------------------------------------------------------*/
.view {
  overflow: hidden;
  clear:both;
  > [class^='col-'],
  .slider__item,
  .card__item,
  &.map .h2{
    @include opacity(0);
    @include transform (translate(0, 125px));
  }

  .content-are-histoire .items {
    @include opacity(0);
    @include transform (translate(0, 125px));
  }


  /* Animations view-on
  ----------------------------------------------------------------------------------------------------*/

  &-on {

    > [class^='col-'],
    .slider__item,
    .card__item,
    &.map .h2, .content-are-histoire .items {
      @include opacity(1);
      @include transform (translate(0, 0));
      @include transition (all 2s cubic-bezier(.19, 1, .22, 1));
    }


    > [class^='col-'],
    .slider__item,
    .card__item {
      @for $ii from 1 through 7 {
        &:nth-child(#{$ii}) {
          @include transition-delay(.20s * $ii);
        }
      }
    }

    .content-are-histoire .items {
      @for $ii from 1 through 20 {
        &:nth-child(#{$ii}) {
          @include transition-delay(.20s * $ii);
        }
      }
    }

  }
}

/* Fade
----------------------------------------------------------------------------------------------------*/
.fade {
  @include opacity(0);
  &-in {
    @include opacity(1);
    @include transition (all 2s cubic-bezier(.19, 1, .22, 1));
  }
}

/* Page load - anim border top
----------------------------------------------------------------------------------------------------*/
.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;

  &-inactive {
    display: none;
  }

  .pace-progress {
    background: $maincolor;
    position: fixed;
    z-index: 2000;
    top: 0;
    right: 100%;
    width: 100%;
    height: 2px;
  }
}