/* Map
----------------------------------------------------------------------------------------------------*/
.map {
  width: 100%;
  position: relative;
  display: block;
  overflow: hidden;

  @media screen and (min-width: $desktop-small) {
    max-height: 400px;
  }

  @include hover {
    img {
      @include transform(scale(1.02));
      @include transition(all .2s ease-in-out);
    }
  }

  img {
    display: block;
    width: 100%;
    height: 100%;
    @include transform(scale(1));
    @include transition(all .2s ease-in-out);
  }

  &.view.view-on {
    .h2 {
      @include transform (translate(0, -21px));

      @media screen and (min-width: $tablet-medium) {
        @include transform (translate(0, -52px));
      }
    }
  }

  .h2 {
    position: absolute;
    z-index: 2;
    top: 50%;
    width: 100%;
    text-align: center;
    color: $textcolor;
    font-size: 36px;
    text-transform: uppercase;

    @media screen and (min-width: $tablet-medium) {
      font-size: 70px;
    }
  }

  &__tang-gourmet{
    &.view.view-on.map{
      .h2{
        @media screen and (max-width: $mobile-small){
          font-size: 25px;
          @include transform(translate(0, -30px));
        }
    }
    }
  }
}