/* Header
 ----------------------------------------------------------------------------------------------------*/
.header {
  min-height: 80px;
  background: $white;
  z-index: 999;
  position:relative;
  @include clearfix;
  @include transition(all 1s ease);
  &>*{
    @include transition(all 1s ease);
  }
  .wrapper {
    max-width: inherit;
    z-index: 11;
    background: $white;
    height: 80px;

    @media screen and (min-width: $desktop-small) {
      padding: 20px 0 0;
    }
  }

  &__logo {
    position: absolute;
    top: 10px;
    left: 50%;
    max-width: 60px;
    margin-left: -30px;
    transition: all 1s ease;
    @include clearfix;

    @media screen and (min-width: $desktop-small) {
      position: relative;
      left: inherit;
      top: inherit;
      float: left;
      max-width: 95px;
      margin: 0;
      margin-left: 35px;
    }

    @media screen and (min-width: $desktop-medium) {
      margin: 0 0 0 42px;
    }
    &-link {
      float: left;
      display: block;
      width: 100%;
    }
  }

  &__img {
    margin: 0 auto 20px;
  }
  &.sticky{
    /*@media screen and (min-width: $desktop-small){*/
      position:fixed;
      width:100%;
      box-shadow: 0 0px 20px $textcolor;
      .header__logo{
        @media screen and (min-width: $desktop-small) {
          max-width: 80px;
        }
      }
      .wrapper{
        padding:0;
      }
      #menu{
        ul{
          margin-bottom: 0;
        }
      }
      #searchform{
        top:0;
      }
    }
  /*}*/
}
.search-active{
  .sticky{
    .form__search{
      top:80px !important;
      @media screen and (min-width: $tablet){
        top:90px !important;
      }
    }
  }
}