/* Extend buttons css
----------------------------------------------------------------------------------------------------*/
%small {
  padding: 15px;
}

%medium {
  padding: 10px 30px;
}

%large {
  padding: 15px 50px;
}

%classic {
  font-size: 13px;
  position: relative;
  border: none;
  color: $maincolor;
  background: rgba(0, 0, 0, 0);
  @include border-radius(0);
  @include transition(all .2s ease-in-out);

  @include hover {
    @include transition(all .2s ease-in-out);
  }
}

/* Button
----------------------------------------------------------------------------------------------------*/
button {
  @extend %classic;
}

.button {
  color: $white;
  @extend %classic;

  /* Button || Classic
  ----------------------------------------------------------------------------------------------------*/
  &__small {
    @extend %small;
  }

  &__medium {
    @extend %medium;
  }

  &__large {
    @extend %large;
  }

  /* Button || Custom
  ----------------------------------------------------------------------------------------------------*/
  &__main {

  }

  &__second {

  }

  &__dropdown {
    background-color: $maincolor;
    cursor: pointer;
  }

  &__search {
    height: 30px;
    width: 30px;
    position: absolute;
    top: 25px;
    right: 25px;
    z-index: 11;
    overflow: hidden;
    padding: 0;

    &:before,
    &:after {
      font-family: tang;
      font-size: 24px;
      color: $color-9;
      position: relative;
      @include transition(all .2s ease-in-out);
    }

    &:before {
      content: '\e811';
      right: 0;
      @include transition(all .2s ease-in-out);

      .search-active & {
        right: -40px;
        @include transition(all .2s ease-in-out);
      }
    }

    &:after {
      content: '\e810';
      right: 60px;
      font-weight: bold;
      @include transition(all .2s ease-in-out);

      .search-active & {
        right: 22px;
        font-size: 20px;
        @include transition(all .2s ease-in-out);
      }
    }
  }

  /* Button || Outline
  ----------------------------------------------------------------------------------------------------*/
  &__outline {
    color: $white;
    background: rgba(0, 0, 0, 0);
    border: 2px solid $white;
    text-align: center;
    text-transform: uppercase;
    display: inline-block;

    @include hover {
      background: $white;
      color: $black;
    }

    &.black {
      color: $black;
      border: 2px solid $black;

      @include hover {
        background: $black;
        color: $white;
      }
    }

    &.red {
      @include hover {
        background: $white;
        color: $color-7;
        border: 2px solid $white;
      }
    }

    &.white {
      @include hover {
        background: $white;
        color: $color-5;
      }
    }
  }
} 