/* Global form css elements
----------------------------------------------------------------------------------------------------*/
#{$all-text-inputs},
textarea {
  width: 100%;
  border: none;
  padding: 10px;
  color: $textcolor;
  background: darken($white, 5%);
  @include border-radius(0);
  @include appearance(none);

  @include placeholder {
    color: lighten($textcolor, 50%);
  }
}

input {

  &[type="submit"] {
    cursor: pointer;
  }
}

/* Label
----------------------------------------------------------------------------------------------------*/
label {
  display: inline-block;
  font-weight: 700;
}

/* Textarea
----------------------------------------------------------------------------------------------------*/
textarea {
  resize: vertical;
}

/* Form layout
----------------------------------------------------------------------------------------------------*/
.form__row {
  position: relative;
  margin: 0 0 20px;
  @include clearfix;

  &:last-child {
    margin-bottom: 0;
  }
}

/* Form
----------------------------------------------------------------------------------------------------*/
.form {

  /* Form || Contact
  ----------------------------------------------------------------------------------------------------*/
  &__contact {
    margin: 35px 0 0;

    @media screen and (min-width: $tablet-medium) {
      margin: 50px 0 0;
    }

    input {

      &[type="email"] {
        background: $color-7;
        color: $white;
        font-size: 14px;
        padding: 18px 20px;
        margin-bottom: 35px;
        @include transition(all .2s ease-in-out);

        @media screen and (min-width: $tablet-medium) {
          margin-bottom: 55px;
        }

        @include placeholder {
          color: $white;
          font-size: 14px;
          font-style: italic;
        }

        &:focus {
          background: rgba($color-7, .8);
          @include transition(all .2s ease-in-out);
        }
      }

      &[type="submit"] {
        font-family: $font-1;
        padding: 15px 65px;
        display: block;
        width: 100%;

        @media screen and (min-width: $tablet-medium) {
          width: auto;
          display: inline-block;
        }
      }
    }
  }

  /* Form || Newsletter
  ----------------------------------------------------------------------------------------------------*/
  &__newsletter {
    position: relative;
    margin: 20px auto 0;

    &--main{
      position: relative;
      max-width:260px;
      margin: 0 auto 10px;
      br{
        display: none;
      }
      .ajax-loader{
        display: none !important;
      }
    }
    .checkbox{
      margin-bottom: 10px;
      .wpcf7-list-item{
        margin:0;
      }
    }
    input {
      &[type="email"] {
        background: $white;
        color: $textcolor;
        font-size: 13px;
        padding: 14px 50px 14px 15px;
        border: 2px solid $textcolor;
        font-family: $font-3;
        @include transition(all .2s ease-in-out);

        @include placeholder {
          color: $textcolor;
          font-size: 13px;
          font-style: italic;
          font-family: $font-3;
        }

        &:focus {
          background: rgba($white, .7);
          @include transition(all .2s ease-in-out);
        }
      }

      &[type="submit"] {
        font-family: $font-1;
        color: $color-9;
        border: none;
        text-transform: uppercase;
        background: rgba(0, 0, 0, 0);
        position: absolute;
        top: 0;
        right: 0;
        text-align: center;
        width: 46px;
        line-height: 46px;
        padding:0 !important;
        &:hover, &:active, &:focus, &:visited{
          background:transparent !important;
          border:none !important;
        }
      }
    }
  }

  /* Form || Search
  ----------------------------------------------------------------------------------------------------*/
  &__search {
    background: $color-3;
    position: absolute;
    height: 75px;
    top: 35px;
    left:0;
    right:0;
    z-index: 10;
    @include transition(all .5s ease-in-out);

    .search-active & {
      margin: 0;
      @include transition(all .5s ease-in-out);
    }

    &:before, .ie-before {
      content: '\e811';
      font-family: tang;
      text-align: center;
      font-size: 24px;
      position: absolute;
      top: 15px;
      right: 0;
      width: calc(100% / 7);
      color: $color-9;

      @media screen and (min-width: $tablet-medium) {
        font-size: 30px;
        width: calc(90% / 7);
      }

    }

    input {

      &[type="text"] {
        color: $textcolor;
        text-align: center;
        font-size: 13px;
        font-family: $font-3;
        background: rgba(0, 0, 0, 0);
        height: 75px;
        position: absolute;
        top: 0;
        left: 0;
        padding: 10px 50px 10px 10px;

        @media screen and (min-width: $tablet-medium) {
          padding: 10px;
        }

        @include placeholder {
          font-style: italic;
        }
      }

      &[type="submit"] {
        position: absolute;
        top: 0;
        right: 0;
        border: none;
        font-size: 0;
        background: rgba(0, 0, 0, 0);
        width: calc(100% / 7);
        height: 75px;
        padding: 0;
        z-index: 2;
      }
    }
  }
}
::-ms-clear {
  display: none;
}