.map_page{
  position: relative;
  z-index: 1;
  .map__top{
    overflow: hidden;
    .item{
      display: flex;
      min-height:90px;
      @media screen and (max-width: $desktop-medium){
        min-height: 70px;
      }
      @media screen and (min-width: 1280px) and (max-width: 1330px){
        min-height: 105px;
      }
      .title{
        color:$white;
        margin-bottom: 0;
        line-height: 70px;
        font-family: $font-1;
        font-size: 13px;
        text-transform: uppercase;
        margin-left:32px;
        margin-right:20px;
        @media screen and (min-width: $desktop-medium + 1px) {
          line-height: 90px;
        }
      }
      @media screen and (max-width: $mobile) {
        display: block;
        height: auto;
        .title{
          line-height: 40px;
        }
      }
    }
    &--left{
      background: $color-17;
      justify-content:flex-end;
      .form-item{
        min-width: 210px;
        width: 210px;
        position: relative;
        max-height:40px;
        margin-top:13px;
        @media screen and (min-width: $desktop-medium + 1px) {
          margin-top: 24px;
        }
        p{
          color:$white;
          margin-bottom: 0;
        }
        input[type="text"]{
          max-width: 100%;
          height:40px;
          background: $white;
          padding-right:35px;
          font-size: 12px;
          &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
            font-size: 13px;
            font-family: $font-3;
            font-style: italic;
          }
          &::-moz-placeholder { /* Firefox 19+ */
            font-size: 13px;
            font-family: $font-3;
            font-style: italic;
          }
          &:-ms-input-placeholder { /* IE 10+ */
            font-size: 13px;
            font-family: $font-3;
            font-style: italic;
          }
          &:-moz-placeholder { /* Firefox 18- */
            font-size: 13px;
            font-family: $font-3;
            font-style: italic;
          }
        }
        input[type="submit"]{
          width: 20px;
          height:20px;
          position: absolute;
          top:10px;
          right:10px;
          z-index: 10;
          background: $transparent;
          border:none;
          box-shadow: none;
        }
        &:nth-child(2):before{
          content:'\e811';
          position: absolute;
          color:$color-9;
          right:10px;
          font-family: $font-7;
          top:6px;
          font-size: 20px;
        }
        &.choose-map{
          margin-left:12px;
          background: -webkit-linear-gradient(left, $color-23 , $color-24); /* For Safari 5.1 to 6.0 */
          background: -o-linear-gradient(right, $color-23, $color-24); /* For Opera 11.1 to 12.0 */
          background: -moz-linear-gradient(right, $color-23, $color-24); /* For Firefox 3.6 to 15 */
          background: linear-gradient(to right, $color-23 , $color-24); /* Standard syntax (must be last) */
          text-align: center;
          p{
            line-height: 40px;
            font-size: 13px;
            text-transform: uppercase;
            display: block;
            position: relative;
            a{
              padding-left:15px;
              color: $white;
              display: block;
              &:hover, &:active, &:focus{
                text-decoration: none;
              }
            }
            &:before{
              content:'\e80c';
              position: absolute;
              color:$white;
              left:40px;
              font-family: $font-7;
              top:0;
              font-size: 18px;
              transition: all .5s;
            }
            &:hover{
              a{
                color: $textcolor
              }
              &:before{
                color: $textcolor;
              }
            }
          }
        }
      }
      .title{
        margin-left: 0 !important;
      }
      @media screen and (min-width:$desktop-medium) and (max-width:$desktop-medium-l){
        .form-item{
          max-width: 180px !important;
          min-width: auto;
        }
      }
      @media screen and (max-width: $desktop-medium){
        width: 100%;
        justify-content: center;
        .title{
          margin-left: 0 !important;
        }
      }
      @media screen and (max-width: $mobile){
        .form-item{
          float: left;
          margin:0 0 10px 0;
          width: 48%;
          &+.form-item{
            margin-left: 4%;
          }
        }
      }
      @media screen and (max-width: $mobile-small){
        overflow: hidden;
        .form-item{
          width: 100%;
          float:none;
          margin:0 0 10px 0 !important;
        }
      }
    }
    &--right{
      background: -webkit-linear-gradient(left, $color-21 , $color-22); /* For Safari 5.1 to 6.0 */
      background: -o-linear-gradient(right, $color-21, $color-22); /* For Opera 11.1 to 12.0 */
      background: -moz-linear-gradient(right, $color-21, $color-22); /* For Firefox 3.6 to 15 */
      background: linear-gradient(to right, $color-21 , $color-22); /* Standard syntax (must be last) */
      margin-bottom: 0;
      .box-check{
         margin:11px 0 0 60px;
        @media screen and (min-width: $desktop-medium) and (max-width: 1760px){
          margin:11px 0 0 30px;
        }
        .form-check{
          input[type="checkbox"]{
            display: none;
            &:checked+label:after{
              display: block;
            }
          }
          label{
            position:relative;
            color: $white;
            font-size: 13px;
            cursor: pointer;
            margin-bottom:0 !important;
            &:before{
              content:"";
              height:12px;
              width: 12px;
              background: $white;
              position: absolute;
              top:4px;
              left:-22px;
            }
            &:after{
              content:"";
              height:8px;
              width: 8px;
              background: $black;
              position: absolute;
              top:6px;
              left:-20px;
              display: none;
            }
          }
        }
        &+.box-check{
          margin-left:95px;
          @media screen and (min-width: $desktop-medium) and (max-width: 1760px){
            margin-left:30px;
          }
        }
      }
      @media screen and (max-width: $mobile){
        .title{
          margin-left: 0 !important;
        }
        .box-check{
          margin-left: 25px !important;
        }
      }
      @media screen and (max-width: $desktop-medium){
        width: 100%;
        justify-content: center;
      }
      @media screen and (max-width: $mobile){
        display: flex !important;
        justify-content: flex-start;
        padding-bottom: 10px;
      }
      @media screen and (max-width: $mobile-small){
        display: block  !important;
        .title{
          line-height: 40px;
        }
        .box-check{
          margin-top: 0px;
        }
      }
    }
  }
  .map__main{
    position: relative;
    @include clearfix;
    #asl-storelocator{
      #map-canvas{
        height:682px !important;
      }
      .agile-modal-content{
        background:$white !important;
        color:$textcolor;
      }
      .asl-panel{
        z-index: 10 !important;
        @include transition(all 1s);
        height:100%;
        @media screen and (min-width:$tablet-medium){
          position:absolute;
          transform: translateX(-100%);
          &.active{
            transform: translateX(0%);
          }
        }
        .search_filter{
            display: none !important;
          }
        #panel{
          padding:0;
          background: $color-4 !important;
          margin:0 !important;
          @media screen and (min-width: $tablet-medium){
            height:682px;
          }
          .panel-inner{
            .item{
              padding:30px 25px 20px 30px;
              border-bottom:none !important;
              cursor: pointer;
              opacity: 1 !important;
              @include transition(all .5s);
              @media screen and (min-width:$mobile-small){
                padding:30px 0 20px 45px;
              }
              @media screen and (min-width:$tablet-big){
                padding:30px 0 20px 85px;
              }
              border:none;
              color:$color-17;
              background: $transparent !important;
              &.highlighted, &:hover{
                .p-title{
                  color:$color-26 !important;
                  a{
                    color:$color-26 !important;
                  }
                }
              }
              &.service3{
                .p-title{
                  span{
                    display: inline-block;
                    left:-37px !important;
                    width: 25px;
                    height:30px;
                    position:absolute;
                    @media screen and (min-width:$tablet-medium){
                      left:-55px !important;
                    }
                  }
                }
                .link{
                  &:hover{
                    background: $color-9 !important;
                    border:2px solid $color-9 !important;
                  }
                }
                &.highlighted,&:hover{
                  .p-title{
                    color:$color-9 !important;
                    a{
                      color:$color-9 !important;
                    }
                  }
                }
              }
              .addr-sec{
                padding-right:10px;
                .p-title{
                  @media screen and (min-width:$tablet-medium){
                    font-size: 24px;
                  }
                  font-family:$font-2;
                  font-size: 20px;
                  text-transform: uppercase;
                  color:$black;
                  position:relative;
                  margin-bottom: 18px;
                  transition: all 0.3s;
                  @media screen and (min-width: $tablet-medium){
                    position: relative;
                    &:before{
                      content:"";
                      left:0;
                      right:0;
                      top:0;
                      bottom:0;
                      background: transparent;
                      z-index: 1;
                      position: absolute;
                    }
                  }
                  a{
                    color:$black;
                    @media screen and (min-width: $tablet-medium){
                      pointer-events: none;
                    }
                  }
                  span{
                    top:0px;
                    left:-35px;
                    position:absolute;
                    @media screen and (min-width:$desktop-small){
                      left:-60px !important;
                      top:8px;
                    }
                  }
                }
                .p-area{
                  font-size: 12px;
                  text-transform: inherit;
                  @media screen and (min-width: $mobile-small){
                    font-size: 16px;
                  }
                  padding-left: 35px !important;
                  padding-right: 20px !important;
                  margin-bottom: 13px;
                  .time_store{
                    display: inline-block;
                    width: 75px;
                  }
                  span[class^="icon-"], span[class*=" icon-"]{
                    position:absolute;
                    font-size: 16px;
                    left:-1px;
                    top:-4px;
                    color:$color-32;
                  }
                  .icon-address{
                    font-size: 20px !important;
                    left:-5px !important;
                  }
                  @media screen and (min-width: $desktop-medium){
                    padding-right:100px !important;
                    .directions{
                      position:absolute;
                      top:-1px;
                      right:-45px;
                      margin:0;
                    }
                  }
                  position:relative;
                  .directions{
                    font-size: 30px;
                    color:$color-17 !important;
                    font-size: 13px;
                    font-family:$font-1;
                    text-decoration: underline;
                    margin:5px 0;
                    a{
                      color:$color-17 !important;
                    }
                  }
                  .glyphicon{
                    height:23px;
                    margin-left:-35px !important;
                    font-size: 20px;
                    margin-top:-5px;
                  }
                }
              }
            }
            .distance{
              margin-top: 23px;
              p{
                margin-left:2px;
                .link{
                  font-size: 13px;
                  border:2px solid $color-17 !important;
                  padding:15px;
                  @media screen and (min-width:$mobile-small){
                    padding:14px 33px;
                  }
                  text-align: center;
                  display: inline-block;
                  color:$color-17;
                  text-transform: uppercase;
                  @include transition(all .5s);
                  &:hover{
                    background: $color-26;
                    color:$white;
                    border:2px solid $color-26 !important;
                  }
                }
              }

            }
          }
          .directions-cont{
            .agile-modal-header{
              padding:15px;
              h4{
                font-size: 20px;
              }
            }
            .rendered-directions{
              table{
                width:95%;
                margin-left: 5px;
                border:1px solid $color-33;
                td{
                  color:$color-33;
                  b{
                    color:$color-33;
                  }
                  .adp-maneuver{
                    webkit-filter: brightness(1);
                    filter: brightness(1);
                    -moz-filter: brightness(1);
                  }
                }
                &.adp-placemark{
                  td{
                    padding-left:10px;
                    border-left:none !important;
                  }
                }
                tr{
                  td{
                    vertical-align: middle;
                  }
                  .adp-substep{
                    vertical-align: middle;
                    border-top: 1px solid $color-33;
                    border-left: 1px solid $color-33;
                  }
                  &>.adp-substep:first-child, &>.adp-substep:nth-child(2){
                    width:25px;
                    text-align: center;
                  }
                  &>.adp-substep:nth-child(3){
                    padding-left:10px;
                  }
                  &>.adp-substep:last-child{
                    width: 50px;
                    text-align: center;
                  }
                }

              }
              .adp-summary{
                margin-left: 5px;
                color:$color-33;
              }
            }
          }
          .mCSB_container{
            margin-right: 0;
          }
          .mCSB_scrollTools{
            top:25px;
            right:5px;
            @media screen and (min-width: $desktop-medium){
              right:8px;
            }
            .mCSB_draggerRail{
              height:95%;
              width: 5px;
              background:$white;
              @include border-radius(0);
            }
            .mCSB_dragger .mCSB_dragger_bar{
              background:$color-2;
              width: 5px; 
              @include border-radius(0);
            }
          }
        }
      }
      .asl-map{
        display: none;
        @media screen and (min-width:$tablet-medium){
          @include transition(all 1s);
          display: block;
          &.active{
            transform: translateX(25%);
          }
        }
        /*.gm-style{
          &.active{
            &>div:first-child{
              transform: translateX(-50%);
            }
          }
        }*/
        .store-locator{
          .infoBox{
            &>img{
              display: none;
            }
            .infoWindow{
              width: 275px;
              padding:10px 10px !important;
              border-radius:0 !important;
              background: $white !important;
              h3{
                background: $transparent !important;
                color:$color-26 !important;
                font-family: $font-2;
                font-size: 15px !important;
                text-transform: uppercase;
                border-bottom:0 !important;
              }
              &.service3{
                h3{
                  color:$color-9 !important;
                }
              }
              .infowindowContent{
                padding-top:5px !important;
                .info-addr{
                  width:100% !important;
                  color:$textcolor !important;
                  .address{  
                    font-family:$font-3;
                    font-size: 12px !important;
                    padding-left:28px;
                    span{
                      font-family: $font-7;
                      margin-left: -28px;
                      &:before{
                        content:'\e80c';
                        font-size: 16px;
                        color:$color-25;
                      }
                    }
                  }
                  &>*:not(:first-child){
                    display: none !important;
                  }
                }

              }
            }
            .asl-buttons{
              display: none !important;
            }
            .arrow-down{
              margin-left: 85px !important;
            }
          }
        }
      }
    }
    h1{
      color:$color-20;
      text-transform: uppercase;
      font-size: 30px;
      margin-bottom: 15px;
      color:$white;
      position: absolute;
      bottom:-100px;
      left:47px;
      font-family: $font-2;
      @media screen and (max-width: $desktop-small){
        bottom:-100px;
        left:15px;
      }
      @media screen and (max-width: $mobile-small){
        font-size: 23px;
      }
    }
    .title_map{
      position: absolute;
      top:50%;
      width: 100%;
      text-align: center;
      transform: translateY(-63%);
      display: inline-block;
      text-transform: uppercase;
      font-size: 70px;
      margin-bottom: 0;
      font-family: $font-2;
      pointer-events:none;
      z-index: 1;
      @media screen and (max-width: $mobile){
        font-size: 40px;
      }
      @media screen and (max-width: $mobile-small){
        font-size: 32px;
      }
    }
  }
}
.infoWindow{
  p{
    font-family: $font-2;
    background: transparent !important;
    color: #f57a01 !important;
    font-size: 15px !important;
    text-transform: uppercase;
    margin-bottom: 0;
  }
  &.service3{
    p{
      color: #e4171b !important;
    }

  }
}
.gmnoprint{
  &.gm-bundled-control{
    display: block;
    &>.gmnoprint{
      display: block;
    }
    .gm-svpc{
      display: none;
    }
  }
  .gm-style-mtc{
    display: none;
  }
}
.gm-fullscreen-control{
  display: none;
}
.gm-style-cc{
  display: none;
}
.mark_hidden{
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: transparent;
}