/* News
----------------------------------------------------------------------------------------------------*/
.news {
  padding: 30px 15px 40px;
  @include clearfix;

  .h2 {
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 0;
    font-size: 36px;

    @media screen and (min-width: $tablet-medium) {
      font-size: 60px;
    }
  }
}