.question{
  @include bg-gradient(90deg, #ef3e11, #d91126);
  padding:100px 47px  75px;
  transition-delay: .2s;
  @media screen and (max-width: $desktop-small){
    padding:100px 15px 30px;
  }
  p{
    font-size: 13px;
    color:$white;
    margin-bottom: 0;
    font-family: $font-3;
    &:first-child{
      font-family: $font-6;
    }
  }
}