.content__actus {
  @media screen and (min-width:$desktop-medium){
  width: calc(100% - 315px);
  }
	&_main{
		@include bg-gradient(0deg, $color-37, $color-38);
		padding:45px 15px;
		@media screen and (min-width: $tablet-medium){
		  padding:45px 35px 50px;
		}
		color: $white;
		h2{
			margin-bottom:10px;
		  	font-size: 30px;
		  @media screen and (min-width:$tablet){
			font-size: 60px;
		  }
		}
	  	.description{
			margin-bottom: 30px;
	  	}
	  	.news{
		  .slider__card {
			@media screen and (min-width: 1260px) {
			  padding: 5px 120px;
			}
			.slick-arrow{
			  &:before{
				color:#3e3a3a;
			  }
			}
		  }
		}
	  	.list_items{
		  margin-bottom: 10px;
		  @media screen and (min-width: $desktop-medium){
			margin:0 -10px 10px -10px;
		  }
		  @include clearfix;
		  .items{
			margin-right: 20px;
			margin-bottom:20px;
			width: 100%;
			@media screen and (max-width: $mobile){
			  max-width: 400px;
			}
			@media screen and (min-width: $mobile-small){
			  width: 100%;
			  margin-right:auto;
			  margin-left:auto;
			  float:none;
			}
			@media screen and (min-width: $tablet-medium){
			  width: calc(50% - 10.4px);
			  margin-right: 20px;
			  float:left;
			  &:nth-child(2n){
				margin-right: 0;
			  }

			}
			@media screen and (min-width: $desktop-medium){
			  width: calc(33.33333% - 20px);
			  margin:0 10px 20px 10px !important;
			}
			padding:0;
			float:left;
			.card{
			  &__item{
				margin:0;
				a{
				  color:$white;
				}
			  }
			  &__img{
				box-shadow:none;
			  }
			  &__content{
				padding:35px 11%;
			  }
			}
		  }
		}
	  .pagination{
		>ul{
		  >li{
			a{
			  color: $white;
			}
		  }
		}
	  }
	  .news{
		background: linear-gradient(122deg,#ff7800,#e85f17);
		margin-bottom: 40px;
	  }
	}
}
.sidebar-actua{
  @media screen and (min-width:$desktop-medium){
	width:315px;
  }
  >ul{
	>li{
	  width: 100%;
	  h2{
		margin-bottom: 0;
		font-family: "Montserrat-Bold";
	  }
	  a{
		font-size:14px;
		color:#333;
		font-weight: normal;
	  }
	  @media screen and (max-width: $tablet){
		padding-left:10px;
		text-align:left;
		a{
		  font-size: 12px;
		}
		i:before{
		  font-size: 35px;
		}
	  }
	  @media screen and (max-width: $mobile){
		float:none;
		width: 100%;
		display: block;
		min-height: 65px;
		a{
		  width:auto;
		}
		span{
		  width: 50px;
		  display: inline-block;
		  i:before{
			font-size: 30px;
		  }
		}
	  }
	}
  }
}