/* Presentation
----------------------------------------------------------------------------------------------------*/
.presentation {
  @include clearfix;

  [class^='col-'] {
    margin: 0;
    padding: 30px 15px;

    @media screen and (min-width: $desktop-small) {
      padding: 50px;
    }
  }

  .en,.ch,.fr {
    display: none;

    &.active {
      display: block;
    }
  }

  /* Presentation || Home
  ----------------------------------------------------------------------------------------------------*/
  &__home {
    background: $color-3;

    @media screen and (min-width: $tablet-medium) {
      background: $white;
    }
    @media screen and (max-width: $tablet-medium - 1px){
      display: block;
    }

    p {
      margin: 0;
      margin-bottom: 10px;
    }

    .second {
      background: $color-3;
      display: none;
      padding:0 15px 30px;
      @media screen and (min-width: $tablet-medium) {
        padding:30px 15px;
        display: block;
      }

      p {
        color: $color-11;
        line-height: 19px;
        font-size: 13px;
      }
    }

    .button{
      font-size: 12px;
      display: block;
      width: 100%;
      text-align: center;
      margin: 20px 0 10px 0;
    }
  }

  &__histoire {
    position: relative;
    z-index: 9;
    .col-5 { padding: 0;  width: 50%; margin-bottom: 0;

      @media screen and (max-width: $tablet ) {

        &.col-equa-height {
          width: 100%; display: block;
        }

      }

      @media screen and (max-width: $mobile ) {  width: 100%; display: block;  }
    }

    .presentationshistoire { text-align: center;

      h2, h1 { padding-top: 48px;
        @media screen and (max-width: $mobile ) { font-size: 36px; padding-top: 20px;}
      }

      .content {  font-size: 13px; height: 378px; font-weight: 400; line-height: 19.03px; text-align: justify; color: $white;
        padding: 15px 70px 0px 94px;  letter-spacing: -0.4px;

        @media screen and (max-width: $tablet ) {
          padding:0 15px 30px;
        }
        @media screen and (max-width: $mobile-small ) {
          height:200px;
        }
      }
    }
    .presentation__cover {
      background-size: cover;
      background-position: center center;
      height: 100%;
      min-height: 340px;
      left: 15px;
      @media screen and (max-width: $mobile){
        background: none !important;
        left:0;
        min-height: auto;
      }
      img{
        display: none;
        @media screen and (max-width: $mobile){
          display: block;
        }
      }
    }

    .col-collaborateurs {  text-align: center; position: relative;  font-size: 15px; font-family: $font-5;
      font-weight: 700; padding: 50px;

      .content { position: absolute;  top: 50%;  @include transform(translate(0px, -50%));    right: 0;  left: 0px;    z-index: -1;

        &:last-child { top: 52%; }

        @media screen and (min-width: $tablet-medium){
          left: 15px;
        }
      }

      @media screen and (max-width: $mobile){
        min-height: 274px !important;
      }

      span { color:  rgba(245, 121, 0, 0.988235); display: block;  font-size: 53.39px; font-weight: 900;  line-height: 1;}
    }

    .bg-gradient-orange { padding: 25px 30px 15px 29px ; color: $white; font-size: 13px; font-weight: 400;

      h2 { font-size: 36px;  text-align: center; margin-bottom: 15px;
        @media screen and (max-width: $mobile ) { font-size: 23px;}
      }

      ul { margin-left: 15px; line-height: 19px;

        li {

          ul {  margin-left: 0;  padding: 1px 14px;  list-style: none; }
        }
      }
    }

    .mCSB_scrollTools .mCSB_draggerRail { width: 5px; background: #ff2254 !important; border-radius: 0px; }
    .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar { background: $white !important; width: 5px; border-radius: 0px; }

  }

  &__recrutement {
    @media screen and (min-width: $tablet-medium){
      display: flex;
    }
    .col-5 {
      width: 100%;
      display: block;
      margin-bottom: 0;

      @media screen and (min-width: $tablet){
        width: 50%;
      }
    }

    &__left {

      &__block {

        padding: 45px 0;

        @media screen and (min-width: $desktop-medium){
          padding: 48px 93px;
        }
        text-align: center;
        h1{
          word-wrap: break-word;
        }
      }


      h1 {
        line-height: 1.1;
        font-size: 30px;
        word-wrap: inherit;
        @media screen and (min-width: $desktop-large ) { font-size: 48px;}
        @media screen and (max-width: $desktop-large - 1px ) { font-size: 41px;}
        @media screen and (max-width: $mobile - 1px ) { font-size: 36px;}
        @media screen and (max-width: $mobile-small) { font-size: 24px;}
      }

      .content {
        font-size: 14px;
        padding: 10px 0 40px;
        text-align: left;
        @media screen and (min-width: $tablet-medium ) { height: 310px;}
        .mCSB_dragger_bar{
          background: $white !important;
        }
        b {
          display: block;
          padding: 20px 0;
        }

      }

      button {
        padding: 15px 24px;
        font-family: $font-1;
        cursor: pointer;
        &:hover {
         color: $color-7 !important;
        }
      }
    }

    &__right {

      padding: 0;

      .col-5 {
        width: 100%;
        display: block;

        @media screen and (min-width: $tablet-medium){
          width: 50%;
          position: relative;

        }
      }

      [class^='col-'] {
        padding-left: 0;
        padding-right: 0;
      }
      &__detail{
        @media screen and (min-width: $tablet-medium){
          position: relative;
        }
        .content{
          padding:30px 15px;
          text-transform: uppercase;
          font-weight: 600;
          @media screen and (min-width: $desktop-medium){
            margin-top: 40px;
            padding:30px;
          }
          p{
            margin-bottom: 15px;
            &:last-child{
              margin-bottom: 0;
            }
            span{
              i{
                font-style: normal;
              }
            }
          }
          @media screen and (min-width: $tablet-medium)and (max-width: $tablet ){
            padding:30px 15px;
            font-size: 14px;
          }
        }
      }
      &__image {
        padding: 75px 0 70px;
        text-align: center;

        @media screen and (min-width: $desktop-medium){
          padding: 80px 0 74px;
        }

        @media screen and (min-width: $tablet-medium)and (max-width: $desktop-medium ){
          padding: 40px 0 70px;
        }
        .content {
          position: absolute;
          left: 0;
          right: 0;
          top: 54%;
          font-size: 14.77px;
          font-family: $font-5;
          font-weight: 700;
          @include transform(translate(0px, -50%));

          span {
            color:  rgba(245, 121, 0, 0.988235);
            display: block;
            font-size: 53.39px;
            font-weight: 900;
            line-height: 0.9;
            font-family: $font-5;
          }
        }
      }

      &__blue {

        padding:  63px 20px;

        @media screen and (min-width: $desktop-medium){
          padding: 83px 50px 88px;
        }
        text-align: center;

        h3, p {
          font-size: 30px;
          font-family: $font-2;
          margin-bottom: 15px;
        }

        i {

          &:before {
            font-size: 50px;
            color: $white;
            transition: all .5s ease;
          }
          &:hover{
            &:before {
              color: $maincolor;
            }
          }
        }
      }

      .presentation__cover {
        background-size: cover;
        background-position: center center;
        min-height: 340px;
        margin-bottom: 0;
      }
    }
  }
}

