/* Key
----------------------------------------------------------------------------------------------------*/
.key {
  padding: 15px 0 5px;
  position: relative;
  background: $white;
  @include clearfix;

  @media screen and (min-width: $tablet-medium) {
    padding: 60px 0;
  }

  p {
    margin: 0;
    text-align: center;
    position: relative;
    width: 100%;
  }

  .desc {
    font-size: 12px;
    font-family: $font-1;
    position: relative;
    top: -5px;

    @media screen and (min-width: $tablet-medium) {
      font-size: 15px;
    }
  }

  [class^='col-'] {
    padding: 30px 15px;
    position: relative;
  }

  .item{
    width: 50%;
    float: left;
    min-height: 135px;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    @media screen and (min-width: $tablet-medium) {
      width: 100% / 12 * 3;
    }

    &:before {
      font-family: tang;
      position: absolute;
      top: 50%;
      left: 50%;
      color: $color-2;
      font-size: 100px;
      transform: translate(-50%, -50%);
      margin: 0;
    }
    &:nth-child(8){
      -webkit-transition-delay: 1.6s;
      -moz-transition-delay: 1.6s;
      transition-delay: 1.6s;
    }
  }

  .caisse{
    &:before {
      content: '\e853';
      font-size: 80px;
      margin-top: -60px;
      margin-left: -40px;
      @media screen and (min-width: $tablet-medium) {
        font-size: 120px;
        margin-top: -80px;
        margin-left: -60px;
      }
    }
  }

  .calendar{
    &:before {
