/* Footer
 ----------------------------------------------------------------------------------------------------*/
.footer {
  padding: 35px 0 20px;
  text-align: center;
  background: $color-3;
  @include clearfix;

  @media screen and (min-width: $tablet-medium) {
    padding: 50px 15px 20px;
  }

  .wrapper{
    max-width: 930px;

    .col-6{
      &:first-child{
        margin-bottom: 20px;

        @media screen and (min-width: $tablet-medium) {
          margin-bottom: 0;
        }
      }
    }
  }

  .h4 {
    text-transform: uppercase;
    color: $textcolor;
    font-family: $font-2;
    font-size: 38px;
    margin: 0;

    @media screen and (min-width: $tablet-medium) {
      font-size: 36px;
    }
  }

  p {
    margin: 0;
  }

  .demo-icon {
    font-size: 30px;
  }
}
.back_to_top{
  width: 40px;
  height:40px;
  border-radius: 50%;
  background: $color-40;
  display: block;
  line-height: 40px;
  text-align: center;
  z-index: 99999;
  position: fixed;
  bottom:100px;
  right:25px;
  @include transition(all .2s);
  visibility: hidden;
  &.show{
    visibility: visible;
  }
  span{
    &:before{
      @include transform(rotate(-90deg));
      font-size: 20px;
      color: $white;
    }
  }
}
.welcome{
  width: 100%;
  padding: 15px 0;
  background: $maincolor;
  z-index: 99999;
  position: fixed;
  bottom:0;
  background: rgba($textcolor, .8);
  &__content{
    position: relative;
    color: $white;
    max-width: 1230px;
    margin:0 auto;
    padding:0 15px;
  }
  h1, h2, h3, h4, h5, h6{
    margin-bottom: 5px;
  }
  p{
    margin-bottom: 5px;
    width: calc(100% - 20px);
    @media screen and (min-width: $tablet-medium) {
      margin:0;
      font-size: 13px;
    }

    font-size: 14px;
  }
  #close-welcome{
    position: absolute;
    right: 15px;
    top: 0;
    color: $white;
    cursor: pointer;
  }
}
.wpcf7-validation-errors{
  display: none !important;
}

.wpcf7-response-output {
  margin: 0 !important;
}
.map_page #asl-storelocator{
  width: 100% !important;
  max-width: none !important;
}