/* Global
----------------------------------------------------------------------------------------------------*/
#main{
  overflow: hidden;
}
.bg {

  &-gradient-red {
    background: rgb(255, 120, 0);
    background: -moz-linear-gradient(-122deg, rgba(255, 120, 0, 1) 0%, rgba(200, 7, 52, 1) 100%);
    background: -webkit-linear-gradient(-122deg, rgba(255, 120, 0, 1) 0%, rgba(200, 7, 52, 1) 100%);
    background: linear-gradient(122deg, rgba(255, 120, 0, 1) 0%, rgba(200, 7, 52, 1) 100%);
    //filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff7800', endColorstr='#c80734', GradientType=1);
  }

  &-gradient-red2 {
    background-image: url("../img/bg-red.png");
    background-size: cover;
    color: $white;
  }
  &-gradient-orange {
    background-color: $color-15;
    background: -webkit-linear-gradient(122deg, rgba(235, 109, 18, 0.8) 7%, rgba(251, 142, 4, 1) 93%);
    background: -moz-linear-gradient(122deg, rgba(235, 109, 18, 0.8) 7%, rgba(251, 142, 4, 1) 93%);
    background: -o-linear-gradient(122deg, rgba(235, 109, 18, 0.8) 7%, rgba(251, 142, 4, 1) 93%);
    background: -ms-linear-gradient(122deg, rgba(235, 109, 18, 0.8) 7%, rgba(251, 142, 4, 1) 93%);
    background: linear-gradient(-32deg, rgba(235, 109, 18, 0.8) 7%, rgba(251, 142, 4, 1) 93%);
  }

  &-gradient-green {
    background-color: $color-16;
    background: -webkit-linear-gradient(122deg, rgba(84, 159, 17, 0.8) 5%, rgba(137, 190, 88, 0.8) 95%);
    background: -moz-linear-gradient(122deg, rgba(84, 159, 17, 0.8) 5%, rgba(137, 190, 88, 0.8) 95%);
    background: -o-linear-gradient(122deg, rgba(84, 159, 17, 0.8) 5%, rgba(137, 190, 88, 0.8) 95%);
    background: -ms-linear-gradient(122deg, rgba(84, 159, 17, 0.8) 5%, rgba(137, 190, 88, 0.8) 95%);
    background: linear-gradient(-32deg, rgba(84, 159, 17, 0.8) 5%, rgba(137, 190, 88, 0.8) 95%);
  }

  &-gradient-blue {
    background-color:  #007bb5;
    background: -webkit-linear-gradient(122deg, rgba(0, 148, 195, 0.8) 7%, rgba(0, 127, 194, 0.8) 93%);
    background: -moz-linear-gradient(122deg, rgba(0, 148, 195, 0.8)  7%, rgba(0, 127, 194, 0.8) 93%);
    background: -o-linear-gradient(122deg, rgba(0, 148, 195, 0.8)  7%, rgba(0, 127, 194, 0.8) 93%);
    background: -ms-linear-gradient(122deg, rgba(0, 148, 195, 0.8)  7%, rgba(0, 127, 194, 0.8) 93%);
    background: linear-gradient(-32deg, rgba(0, 148, 195, 0.8)  7%, rgba(0, 127, 194, 0.8) 93%);
  }

  &-gradient-gray {
    background-color: $color-3;
  }
  &-red{
    @include bg-gradient(90deg, #ef3e11, #d91126);
    .content__page{
      color: $white;
    }
  }

  &-full__width {
    background-size: cover !important;
    background-position: center center;
    height: 100%;
    min-height: 300px;

    @media screen and (max-width: $tablet-medium) {
      min-height: 487px;
    }
  }
  &-black {
    background: $color-17;
  }

  &-green {
    background: $green;
  }
}

.button-gradient-orange {
  background: rgb(255, 120, 0);
  background: -moz-linear-gradient(-122deg, rgba(255, 120, 0, 1) 0%, rgba(200, 7, 52, 1) 100%);
  background: -webkit-linear-gradient(-122deg, rgba(255, 120, 0, 1) 0%, rgba(200, 7, 52, 1) 100%);
  background: linear-gradient(122deg, rgba(255, 120, 0, 1) 0%, rgba(200, 7, 52, 1) 100%);
  font-size: 12px;
  //filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff7800', endColorstr='#c80734', GradientType=1);
}

.col-equa-height {
  display: table-cell;
  width: 50%;
  vertical-align: top;
  float: none !important;
}

.img-line__height {
  line-height: 0;

  img {
    height: 100%;
    @media screen and (max-width: $mobile) {
      height: auto;
    }
  }
}

.border {
  &_light_red {
    background: $color-27;;
  }
  &_green {
    background: $green;
  }
  &_red {
      background: $red;
  }
  &_blue {
      background: $blue;
  }
  &_orange {
      background: $orange;
  }
  &_purple {
    background: $purple;

  }
}

/* Icon
----------------------------------------------------------------------------------------------------*/
.icon {
  position: relative;
  padding-left: 20px;
}

.demo-icon {

  .chrono &,
  .timer & {
    position: absolute;
    color: $color-6;
  }

  .chrono & {
    font-size: 18px;
    top: 1px;
    left: -7px;
  }

  .timer & {
    font-size: 16px;
    top: 1px;
    left: -7px;
  }

  &.icon-facebook {
    color: #3b599b;
  }
  &.icon-twitter {
    color: #55acee;
  }
  &.icon-linkedin {
    color: #0e76a8;
  }
  &.icon-youtube {
    color: #ff3333;
  }
  &.icon-insta {
    color: #125688;
  }
}

.content__page  {
  padding: 30px 0;
}
h1.h2{
  @media screen and (max-width: $tablet-big){
    font-size: 45px;
  }
  @media screen and (max-width: $mobile-small){
    font-size: 36px;
  }
}