/* Social
 ----------------------------------------------------------------------------------------------------*/
.social {

  a{
    display: inline-block;
    
    @include hover{
      .demo-icon{
        color: $maincolor;
        @include transition(all .2s ease-in-out);
      }
    }

    .demo-icon{
      font-size: 38px;
      @include transition(all .2s ease-in-out);

      @media screen and (min-width: $tablet-medium) {
        font-size: 50px;
      }
    }
  }
}