/* Breadcrumb
----------------------------------------------------------------------------------------------------*/
.breadcrumb {
  position: relative;
  background: $color-3;

  ol { color: $color-1; padding: 0px 25px;  margin: 0;  font-size: 11px;

    li, > span { padding: 4px 5px;  display: inline-block;

      &:first-child {
        a { font-size: 12px;

          i { font-size: 15px; }
        }
      }

      &:last-child {

        a,span {color: $color-19;font-weight: 700; }

      }
    }

    a {  color: $color-11;  }
  }
  li{
    &:first-child{
      span{
        a{
          span{
            display: none;
          }
        }
      }
    }
  }

  &.breadcrumb__sidebar { background: $color-4;

    ol { padding: 2px 41px;
      li {
        @media screen and (max-width: $tablet-medium){
          padding: 4px 2px;
        }
      }

      @media screen and (max-width: $tablet-medium){
        padding: 2px 30px;
      }
    }

    //@media screen and (min-width: $tablet-medium)and (max-width: $tablet){
    //  width: 25%;
    //}
    //@media screen and (min-width: $tablet){
    //  width: 315px;
    //}
  }

  &__haft {
    background: $white;

    @media screen and (min-width:$desktop-medium){
      width: 50%;
    }
  }

  &__white {
    background: $white;
  }
  @media screen and (max-width: $mobile ) { display: none;   }
  &-product{
    position: absolute !important;
    z-index: 1;
  }
}