body {
  font-size: $base-font-size;
  line-height: $base-line-height;
  font-family: $font-3;
  color: $textcolor;
  -webkit-font-smoothing: antialiased;
  padding: 0;
  margin: 0;

  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($black, 0);
    z-index: 10;
    pointer-events: none;
    @include transition(all .2s ease-in-out);
  }

  &.menu-mobile--active{
    overflow: hidden;
  }

  &.search-active{
    overflow: hidden;

    /* @media screen and (min-width: $desktop-small) {
      margin-right: 15px;
    } */

    &:before{
      background: rgba($black, .8);
      @include transition(all .2s ease-in-out);
      height:2000px;
    }
  }
}

a {
  color: $maincolor;
  text-decoration: none;
  @include transition(all .2s ease-in-out);

  @include hover {
    color: $maincolor;
    @include transition(all .2s ease-in-out);
  }

  img {
    display: block;
    margin: 0 auto;
  }
}

/* Headings
----------------------------------------------------------------------------------------------------*/

h1, .h1 {
  font-size: 40px;
}

h2, .h2 {
  font-size: 60px;
}

h3, .h3 {
  font-size: 28px;
}

h4, .h4 {
  font-size: 24px;
}

h5, .h5 {
  font-size: 20px;
}

h6, .h6 {
  font-size: 16px;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  line-height: $heading-line-height;
  font-family: $font-2;
}

/* Lists
----------------------------------------------------------------------------------------------------*/
ol,
ul {
  margin: 0;
  padding: 0;

  ol,
  ul {
    padding: 0 0 0 40px;
  }
}

li {
  margin: 0;
}

/* Spacing
----------------------------------------------------------------------------------------------------*/
p,
dl,
hr,
h1,
h2,
h3,
h4,
h5,
h6,
ol,
ul,
pre,
table,
address,
fieldset,
figure {
  margin: 0 0 20px 0;
}

/* Wysiwyg style
----------------------------------------------------------------------------------------------------*/
hr {
  clear: both;
  border: 0;
  height: 1px;
  background: $white;
}

blockquote {
  border-left: 4px solid #eceeef;
  padding: 20px;
  margin: 0 0 20px 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;

  p:last-child {
    margin: 0;
  }
}

cite {
  font-style: italic;
}

small {
  font-size: 11px;
}

table {
  margin: 20px 0;
  width: 100%;
  border: $black;
  border-collapse: collapse;
  border-left: 0;
  @include border-radius(3px);

  th {
    padding: 10px;
    border-left: 1px solid $black;
    border-top: 0;
    vertical-align: top;
    text-align: left;
  }
  td {
    padding: 10px;
    border-top: 1px solid $black;
    border-left: 1px solid $black;
    vertical-align: top;
    text-align: left;
  }
}

address {
  display: block;
  margin-bottom: 20px;
  font-style: normal;
  line-height: 20px;
}

abbr {
  text-decoration: underline;
}

abbr[title], abbr[data-original-title] {
  cursor: help;
  border-bottom: 1px dotted $black;
}

big {
  font-size: larger;
}

s,
strike,
del {
  text-decoration: line-through;
}

em,
var {
  font-style: italic;
}

ins {
  background-color: $white;
  text-decoration: none;
}

tt,
kbd,
samp {
  font-family: monospace;
}

pre {
  display: block;
  padding: 10px;
  margin: 0 0 20px;
  word-break: break-all;
  white-space: pre-wrap;
  background-color: $white;
  border: 1px solid $black;
  border-radius: 3px;
}

q {
  display: inline;
  font-style: italic;
}

sub,
sup {
  bottom: -0.25em;
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

/* Text
----------------------------------------------------------------------------------------------------*/
.text {

  &__left {
    text-align: left !important;
  }

  &__center {
    text-align: center !important;
  }

  &__right {
    text-align: right !important;
  }

  &__lowercase {
    text-transform: lowercase !important;
  }

  &__uppercase {
    text-transform: uppercase !important;
  }

  &__capitalize {
    text-transform: capitalize !important;
  }

  &__justify {
    text-align: justify !important;
  }

}

/* Text color
----------------------------------------------------------------------------------------------------*/
.text {

  &__white {
    color: $white !important;
  }

  &__gradient__red {
    color: #fa4e38;
  }

  &__gradient__orange {
    color: #ffb310;
  }

  &__gradient__green {
    color: #a6da77;
  }

  &__gradient__gray {
    color: $textcolor !important;
  }

  &__black {
    color: $color-17;
  }
  &__grey {
    color: $textcolor;
  }
}

.number {
  color: $maincolor;
  font-size: 28px;
  font-family: $font-2;

  @media screen and (min-width: $tablet-medium) {
    font-size: 36px;
  }
}

/* Title
----------------------------------------------------------------------------------------------------*/
.title {

  &__page {
    padding: 20px 0;
  }

  &__large {
    font-size: 28px;
    font-family: $font-2;

    @media screen and (min-width: $tablet-medium) {
      font-size: 62px;
    }
  }

  &__medium {
    font-size: 14px;
    font-family: $font-2;

    @media screen and (min-width: $tablet-medium) {
      font-size: 32px;
    }
  }

  &__transparent {
    color: $white;
  }

  /* Title || Presentation
  ----------------------------------------------------------------------------------------------------*/
  &__presentation {
    text-transform: uppercase;
    color: $maincolor;
    font-size: 22px;
    line-height: 30px;
    font-family: $font-2;

    @media screen and (min-width: $tablet-medium) {
      font-size: 30px;
      line-height: 40px;
    }
  }

  /* Title || Offer
  ----------------------------------------------------------------------------------------------------*/
  &__offer {
    color: $white;
    font-size: 20px;
    text-transform: uppercase;
    display: none;

    @media screen and (min-width: $tablet-medium) {
      display: block;
    }
  }
}


