
.zmshbt.default  {
  display: none !important;
}

.share_social {
  position: absolute;
  right: 22px;
  width: 182px;
  bottom: 35px;

  @media screen and (min-width: $tablet-medium){
    bottom: 17px;
  }

  .zmshbt.default {     margin-top: -7px; float: right; display: block !important;

    a {
      font-family: "tang";
      background-image: none !important;
      font-style: normal;
      font-weight: normal;
      speak: none;
      display: inline-block;
      text-decoration: inherit;
      text-align: center;
      margin: 0;
      width: 30px;
      margin-right: 5px;
      height: auto;
      @include transition(all 0.2s ease-in-out);

      &:before {
        font-size: 30px;
      }

      &:hover {
        transform: scale(1);
        color: $maincolor !important;
      }

      &.facebook {
        color: #3b599b;
        &:before {

          content: '\e808';
        }
      }

      &.twitter {
        color: #55acee;
        &:before {

          content: '\e809';
        }
      }

      &.linkedin {
        color: #0e76a8;
        &:before {

          content: '\e80a';
        }
      }
    }
  }

  span {
    float: left;
    padding-top: 6px;
    font-size: 13px;
    font-weight: 900;
  }

  ul {
    list-style: none;
    float: right;
    margin: 0;

    li {
      float: left;

      a {

        i { @include transition(all 0.2s ease-in-out);
          &:before {
            font-size: 30px;
            margin-right: 4px;
          }
        }

        &:hover {
          i {
            color: #f67901;
          }
        }
      }
    }
  }
}
.produits__details {
  overflow: visible;
  @include clearfix();
  .col-6 { padding: 0;

    &:last-child { margin-bottom: 0px; }
  }

  &__left{
    padding: 40px 15px 75px;
    text-align: center;
    position: relative;
    @media screen and (min-width: $tablet-medium){
      padding: 103px 15px 128px ;
    }
    &>img{
      max-height: 350px;
    }
  }
  &__right {
    background: $color-3;
    padding: 30px 15px;

    .return__back { @include transition(all 0.2s ease-in-out);
      position: absolute;
      right: 34px;
      top: 8px;
      font-size: 11px;
      color: $color-17;

      span,i {
        display: inline-block;
        vertical-align: middle;
      }
      i {
        &:before { font-size: 20px;    margin-left: 7px; }
      }

      @media screen and (min-width: $tablet-medium){
        top: 10px;
      }

      &:hover {
        color: $color-19 ;
      }
    }

    @media screen and (min-width: $tablet-medium){
      padding: 55px 35px 43px 43px;
     // margin-top: -29px;
    }

    h3, h1 {
      font-size: 30px;
      @media screen and (max-width: $tablet-big){
        font-size: 23px;
      }
    }


    .content_produits__detail {
      height: 410px ;
      margin-top: 45px;

      h4 {
        font-size: 16px;
        margin-bottom: 12px;
      }

      hr{
        border-bottom: 1px solid $white;
        margin-bottom: 14px;
        margin-top: 17px;
        font-size: 13px;

        &:last-child {
          border-bottom: none;
        }
      }

      .mCS-dark.mCSB_scrollTools .mCSB_draggerRail {
        background: #ffffff !important;
        width: 5px;
      }

      .mCS-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
        width: 5px;
        background: $color-28 !important;
      }
    }
  }

}

