/* Card
 ----------------------------------------------------------------------------------------------------*/
.card {
  position: relative;

  /* Card || Item
  ----------------------------------------------------------------------------------------------------*/
  &__item {
    position: relative;
    display: block;
    margin: 20px;
    @include clearfix;
    a {
      display: block;
      @include clearfix;

      @include hover {
        img {
          @include transform(scale(1.05));
          @include transition(all .2s ease-in-out);
        }

        .card__content {
          &:before {
            right: 10px;
            @include transition(all .2s ease-in-out);

            @media screen and (min-width: $desktop-small) {
              right: 30px;
            }
          }
        }
      }
    }

    .h3 {
      text-transform: uppercase;
      font-size: 20px;
      margin: 0 0 5px 0;
      word-break: break-word;
      color:$white;
      @media screen and (min-width: $desktop-small) {
        font-size: 23px;
        margin: 0 0 10px 0;
      }
    }

    .date {
      font-size: 12px;
      margin: 0;
      font-family: $font-1;
    }
  }

  /* Card || Img
  ----------------------------------------------------------------------------------------------------*/
  &__img {
    overflow: hidden;
    @include box-shadow(0, 0, 20px, 0, rgba(0, 0, 0, 0.7));

    img {
      display: block;
      height: 100%;
      width: 100%;
      @include transform(scale(1));
      @include transition(all .2s ease-in-out);
    }
  }

  /* Card || Content
  ----------------------------------------------------------------------------------------------------*/
  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 25px 20px;

    @media screen and (min-width: $tablet-medium) {
      padding: 15px;
    }

    @media screen and (min-width: $desktop-small) {
      padding: 20px;
    }

    @media screen and (min-width: $desktop-medium) {
      padding: 35px 14%;
    }

    &:before {
      content: '\e80f';
      font-family: tang;
      position: absolute;
      bottom: 0;
      color: $white;
      font-size: 26px;
      z-index: 2;
      right: 20px;
      @include transition(all .2s ease-in-out);

      @media screen and (min-width: $desktop-small) {
        bottom: 30px;
        right: 45px;
      }
    }

    &:after {
      content: '';
      background: rgba($black, .4);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: block;
      z-index: 0;
    }
  }

  /* Card || Header
  ----------------------------------------------------------------------------------------------------*/
  &__header {
    min-height: 65px;
    position: relative;
    z-index: 2;

    @media screen and (min-width: $desktop-small) {
      min-height: 90px;
    }
  }

  /* Card || Body
  ----------------------------------------------------------------------------------------------------*/
  &__body {
    z-index: 2;
    position: relative;
    font-size: 13px;
    line-height: 20px;
    margin: 0;
    max-height: 120px;
    overflow: hidden;
    @media screen and (min-width: $tablet-medium) {
      max-height: 62px;
    }

    @media screen and (min-width: $desktop-small) {
      max-height: inherit;
    }
  }
}