/* Menu || Burger
----------------------------------------------------------------------------------------------------*/
.button__menu {

  /* Menu || Burger || Container
  ----------------------------------------------------------------------------------------------------*/
  &-container {
    position: absolute;
    top: 25px;
    left: 25px;
    padding: 5px;
    z-index: 998;
    background: rgba(0, 0, 0, 0);
    @include clearfix;

    @media screen and (min-width: $desktop-small) {
      display: none;
    }

    button {
      cursor: pointer;
    }
  }

  /* Menu || Burger || Open
  ----------------------------------------------------------------------------------------------------*/
  &-open {
    display: block;
    float: left;
    width: 26px;
    height: 22px;
    position: relative;
    @include rotate(0);
    @include transition (all .5s ease-in-out);

    &.open {

      span {
        &:nth-child(1),
        &:nth-child(6) {
          @include rotate(45);
        }

        &:nth-child(2),
        &:nth-child(5) {
          @include rotate(-45);
        }

        &:nth-child(1) {
          left: 2px;
          top: 4px;
        }

        &:nth-child(2) {
          left: calc(50% - 2px);
          top: 4px;
        }

        &:nth-child(3) {
          left: -50%;
          opacity: 0;
          @include opacity(0);
        }

        &:nth-child(4) {
          left: 100%;
          @include opacity(0);
        }

        &:nth-child(5) {
          left: 2px;
          top: 13px;
        }

        &:nth-child(6) {
          left: calc(50% - 2px);
          top: 13px;
        }

      }
    }

    span {
      display: block;
      position: absolute;
      height: 2px;
      width: 50%;
      background: $maincolor;
      @include rotate(0);
      @include transition (all .25s ease-in-out);

      &:nth-child(even) {
        left: 50%;
        @include border-radius(0 9px 9px 0);
      }

      &:nth-child(odd) {
        left: 0;
        @include border-radius(9px 0 0 9px);
      }

      &:nth-child(1),
      &:nth-child(2) {
        top: 0;
      }

      &:nth-child(3),
      &:nth-child(4) {
        top: 10px;
      }

      &:nth-child(5),
      &:nth-child(6) {
        top: 20px;
      }
    }
  }

  /* Menu || Burger || Close
  ----------------------------------------------------------------------------------------------------*/
  &-close {
    display: none;
    width: 36px;
    height: 32px;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0);

    .menu-mobile--active & {
      display: block;
      z-index: 50;
    }
  }
}

/* Menu || Mobile
----------------------------------------------------------------------------------------------------*/
.menu {
  z-index: 998;
  position: fixed;
  top: 80px;
  left: -100%;
  bottom: 0;
  width: 100%;
  background: $white;
  border-top: 1px solid $color-3;
  overflow-y: auto;
  background: rgba(0, 0, 0, .8);
  -webkit-overflow-scrolling: touch;
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0);
  @include clearfix;
  @include transition (all 0.5s ease-out);
  .menu-mobile--active & {
    left: 0;
  }

  .wrapper {
    padding: 0;
    background: $white;
    height: auto;
    @include clearfix;

    .menu-mobile--active & {
      padding: 0 15px;
    }
  }

  ul {
    list-style: none;
    margin: 0;
    @include clearfix;
  }

  li {
    float: left;
    text-align: center;
    position: relative;
    width: 100%;
    border-bottom: 1px solid $color-14;
    padding: 0;

    &.current_page_item{
      &:before{
        color: #ffa800;
      }
      a{
        span {

          position: relative;
          &:before {
            content: '';
            position: absolute;
            bottom: -2px;
            left: 0;
            height: 2px;
            width: 0;
            background: $color-15;
            @include transition(all .2s ease-in-out);
          }

          &:before {
            width: 100%;
            @include transition(all .2s ease-in-out);
          }
        }
      }
    }

    &:nth-last-child(2) {
      border: none;
    }

    &:before,
    &:after {
      font-family: tang;
      color: $maincolor;
      position: absolute;
      pointer-events: none;
      @include transition(all .2s ease-in-out);
    }

    @include hover {
      &:before {
        color: $color-15;
        @include transition(all .2s ease-in-out);
      }
    }

    &.story:before {
      content: '\e817';
      font-size: 32px;
      top: 10px;
      left: 6px;
    }

    &.product:before {
      content: '\e816';
      font-size: 37px;
      top: 5px;
      left: 2px;
    }
    &.recipe:before {
      content: '\e815';
      font-size: 33px;
      top: 6px;
      left: 4px;
    }
    &.news:before {
      content: '\e814';
      font-size: 25px;
      top: 13px;
      left: 9px;
    }
    &.shop:before {
      content: '\e813';
      font-size: 37px;
      top: 6px;
      left: 2px;
    }
    &.caterer {
      @media screen and (max-width: 1420px){
        flex:0 0 175px !important;
      }
      span {
        margin-top: -14px;
      }

      &:before {
        content: '\e818';
        font-size: 37px;
        top: 6px;
        left: 2px;
      }
    }
    &.search:before,
    &.search:after {
      content: '\e811';
      font-size: 26px;
      top: 21px;
      left: 12px;
      color: $color-9;
    }

    &.search:after {
      content: '\e810';
    }
  }

  a {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 700;
    color: $textcolor;
    display: block;
    text-align: left;
    width: 100%;
    height: 100%;
    line-height: 63px;
    padding: 0 15px 0 60px;

    p{
      margin: 0;
    }
  }

  /* Menu || Tablette / PC
  ----------------------------------------------------------------------------------------------------*/
  @media screen and (min-width: $desktop-small) {
    position: static;
    left: auto;
    overflow: inherit;
    background: none;
    padding: 0;
    float: right;
    width: calc(100% - 150px);
    border-top: none;
    padding-left: 40px;

    ul{
      margin-bottom: 20px;
      @include transition(all 1s ease);
      @media screen and (min-width: $tablet){
        @include flex();
      }
    }

    li {
      //width: calc(100% / 7);
      height: 90px;
      border: none;
      overflow: hidden;
      &.current_page_item{
        &:before{
          color: #ffa800;
        }
        a{
          span {
            &:before {
              width: 100%;
              @include transition(all .2s ease-in-out);
            }
          }
        }
      }

      &:before,
      &:after{
        position: relative;
        left: 0 !important;
      }

      &.story:before {
        content: '\e817';
        font-size: 34px;
        top: 12px;
      }

      &.product:before {
        content: '\e816';
        font-size: 40px;
        top: 7px;
      }
      &.recipe:before {
        content: '\e815';
        font-size: 38px;
        top: 8px;
      }
      &.news:before {
        content: '\e814';
        font-size: 28px;
        top: 20px;
      }
      &.shop:before {
        content: '\e813';
        font-size: 41px;
        top: 5px;
      }
      &.caterer {

        span {
          margin-top: -14px;
        }

        &:before {
          content: '\e812';
          font-size: 86px;
          top: -20px;
        }
      }
      &.search:before {
        content: '\e811';
        font-size: 26px;
        top: 21px;
        color: $color-9;
        left: 14px !important;

        .search-active &{
          left: 100% !important;
        }
      }

      &.search:after {
        left: -100% !important;

        .search-active &{
          left: -14px !important;
        }
      }
    }

    a {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      line-height: normal;
      padding: 0 15px;

      @include hover {
        span {
          &:before {
            width: 100%;
            @include transition(all .2s ease-in-out);
          }
        }
      }

      p {
        position: absolute;
        bottom: 10px;
        left: 0;
        width: 100%;
        text-align: center;
      }

      span {
        position: relative;
        display: inline-block;
        &:before {
          content: '';
          position: absolute;
          bottom: -2px;
          left: 0;
          height: 2px;
          width: 0;
          background: $color-15;
          @include transition(all .2s ease-in-out);
        }
      }
    }

    .search {
      a {
        span {
          &:before {
            background: $color-9;
          }
        }
      }
    }
  }
  @media screen and (min-width: $desktop-medium){
    padding-left:75px;
  }

  /* Menu || Footer
  ----------------------------------------------------------------------------------------------------*/
  &__footer {
    margin: 20px 0 0 0;

    @media screen and (min-width: $tablet-medium) {
      margin: 35px 0 0 0;
    }

    ul {
      margin: 0 0 2px 0;
    }

    li {
      list-style: none;
      display: inline-block;
      padding: 0 10px;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        top: 25%;
        right: 0;
        height: 50%;
        width: 1px;
        background: $textcolor;
      }

      &:last-child {
        &:before {
          display: none;
        }
      }
    }

    a {
      font-size: 12px;
      color: $textcolor;
      display: block;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        bottom: -2px;
        left: 0;
        width: 0;
        height: 2px;
        background: $black;
        @include transition(all .2s ease-in-out);
      }

      @include hover {
        &:before {
          width: 100%;
          @include transition(all .2s ease-in-out);
        }
      }
    }

    p {
      color: $color-10;
      font-size: 12px;
    }
  }
}
.form__search{
  background:$white;
  @media screen and (max-width:$desktop-small){
    top:0px;
  }
  @media screen and (max-width: $tablet-big){
    height: 50px;
    &:before{
      line-height: 20px;
    }
    input[type='text'], input[type="submit"]{
      height:50px;
    }
  }
}
//.form__search {
//  @media screen and (max-width: $desktop-small) {
//    top: 0;
//  }
//}
.search-active{
  .form__search{
    background:$color-3;
    top:130px;
    @media screen and (max-width:$tablet-big){
      top:80px;
    }
  }
}