.job__detail{
  padding:20px 15px;
  @media screen and (min-width: $desktop-large){
    padding:50px 80px;
  }
  &>h2{
    font-size: 36px;
    text-transform: uppercase;
    text-align: center;
  }
  .single_job_listing{
    .job-manager-applications-applied-notice{
      margin:0 !important;
    }
    .company,.job_description, .job-listing-meta{
      display: none;
    }
    .application_details{
      display: block !important;
      border:none;
      padding:0;
      box-shadow: none;
      &:after, &:before{
        display: none;
      }
    }
  }
}
.job-manager-application-form{
  .g-recaptcha{
    margin-bottom: 30px;
    @media screen and (max-width: $mobile-small) {
      margin-bottom: 10px;
    }
    &>div{
      margin:0 auto;
      transform: scale(0.9);
      @media screen and (min-width: $mobile-small) {
        transform: scale(1);
      }
    }
  }
  fieldset{
    margin-bottom: 5px;
    padding-bottom: 0;
    border:none;
    @media screen and (min-width: $mobile-small){
      margin-bottom: 10px;
      padding-bottom: 15px;
    }
    label{
      width: 100%;
      @media screen and (min-width: $mobile-small) {
        width: 205px;
      }
      font-size: 14px;
    }
    div.field{
      @media screen and (min-width: $mobile-small) {
        width: calc(100% - 205px);
      }
      width:  100%;
    }
    &.fieldset-upload{
      label{
        width: 100%;
        @media screen and (min-width: $tablet) {
          width: 205px;
        }
        font-size: 14px;
      }
      div.field{
        &>p{
          @media screen and (max-width: $mobile-small){
            margin:0;
          }
          label{
            width: 150px;
            text-align: center;
            border: 1px solid #000;
            margin-right: 10px;
            font-size: 15px;
          }
          span{
            font-size: 13px;
            @media screen and (min-width: $tablet-medium) and (max-width: $desktop-medium){
              display: block;
              width: 100%;
              clear: both;
            }
            @media screen and (max-width: $mobile-small){
              display: block;
              width: 100%;
              clear: both;
            }
          }
        }
        input[type='file']{
          display: none;
        }
        small.description{
          margin:0;
        }
        @media screen and (min-width: $tablet) {
          width: calc(100% - 205px);
        }
        width:  100%;
      }
    }
  }
  .message-box{
    textarea{
      height:200px;
      @media screen and (min-width: $tablet-medium) {
        height: 318px;
      }
    }
  }
  .wp_job_manager_send_application_button{
    text-transform: uppercase;
    padding: 20px 30px;
    font-weight: 600;
    margin: 0 auto;
    display: block;
    background: $maincolor;
    color:$white;
    transition: all 1s;
    &:hover{
      background: $color-9;
    }
  }
}