/* Mixins || Hover
----------------------------------------------------------------------------------------------------*/
@mixin hover {
  &:hover, &:active, &:focus {
    @content;
  }
}

/* Mixin || Clearfix
----------------------------------------------------------------------------------------------------*/
@mixin clearfix {
  &:after {
    content: "";
    display: block;
    clear: both;
  }
}

/* Mixin || Transition
----------------------------------------------------------------------------------------------------*/
@mixin transition($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  -ms-transition: $args;
  -o-transition: $args;
  transition: $args;
}

/* Mixin || Opacity
----------------------------------------------------------------------------------------------------*/
@mixin opacity($value) {
  $IEValue: $value*100;
  opacity: $value;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity="+$IEValue+")";
  filter: alpha(opacity=$IEValue);
}

/* Mixin || Border radius
----------------------------------------------------------------------------------------------------*/
@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  -ms-border-radius: $radius;
  border-radius: $radius;
}

/* Mixin || Display Flex
----------------------------------------------------------------------------------------------------*/
@mixin flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

/* Mixin || Box-shadow
----------------------------------------------------------------------------------------------------*/
@mixin box-shadow($top, $left, $blur, $color, $inset:"") {
  -webkit-box-shadow: $top $left $blur $color #{$inset};
  -moz-box-shadow: $top $left $blur $color #{$inset};
  -o-box-shadow: $top $left $blur $color #{$inset};
  box-shadow: $top $left $blur $color #{$inset};
}

/* Mixin || Transform-origin
----------------------------------------------------------------------------------------------------*/
@mixin transform-origin($origin) {
  moz-transform-origin: $origin;
  -o-transform-origin: $origin;
  -ms-transform-origin: $origin;
  -webkit-transform-origin: $origin;
  transform-origin: $origin;
}

/* Mixin || Transform
----------------------------------------------------------------------------------------------------*/
@mixin transform($transforms) {
  -moz-transform: $transforms;
  -o-transform: $transforms;
  -ms-transform: $transforms;
  -webkit-transform: $transforms;
  transform: $transforms;
}

/* Mixin || Rotation
----------------------------------------------------------------------------------------------------*/
@mixin rotate($deg) {
  @include transform(rotate(#{$deg}deg));
}

/* Mixin || Scale
----------------------------------------------------------------------------------------------------*/
@mixin scale($scale) {
  @include transform(scale($scale));
}

/* Mixin || Keyframes animation
----------------------------------------------------------------------------------------------------*/
@mixin keyframes($animation-name) {
  @-webkit-keyframes #{$animation-name} {
    @content;
  }
  @-moz-keyframes #{$animation-name} {
    @content;
  }
  @-ms-keyframes #{$animation-name} {
    @content;
  }
  @-o-keyframes #{$animation-name} {
    @content;
  }
  @keyframes #{$animation-name} {
    @content;
  }
}

/* Mixin || Animation
----------------------------------------------------------------------------------------------------*/
@mixin animation($str) {
  -webkit-animation: #{$str};
  -moz-animation: #{$str};
  -ms-animation: #{$str};
  -o-animation: #{$str};
  animation: #{$str};
}

/* Mixin || Text truncate
----------------------------------------------------------------------------------------------------*/
@mixin text-truncate() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Mixin || Background Gradient
----------------------------------------------------------------------------------------------------*/
@mixin bg-gradient($arrow, $color1, $color2) {
  background: #{$color1}; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(#{$arrow}, #{$color1}, #{$color2}); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(#{$arrow}, #{$color1}, #{$color2}); /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(#{$arrow}, #{$color1}, #{$color2}); /* For Firefox 3.6 to 15 */
  background: linear-gradient(#{$arrow}, #{$color1}, #{$color2}); /* Standard syntax */
}