.block__offer {

  padding: 30px 0;

  @media screen and (min-width: $tablet-medium){
    padding:60px 0 90px;
  }

  [class^='col-'] {
    padding: 0 10px;

    @media screen and (max-width: $mobile){
      margin-bottom: 20px;
    }
  }

  &__block {
    padding: 25px 15px;
    @media screen and (min-width: $desktop-medium){
      padding: 27px 146px 32px;
    }

    @media screen and (min-width: $tablet-medium) and (max-width:  $desktop-medium ){
      padding: 27px 50px 32px;
    }
  }
  .mCSB_container.mCS_no_scrollbar_y.mCS_y_hidden{
    margin-right: 30px;
  }
  &__blockCSB {
    padding: 50px 15px;
    @media screen and (max-width: $mobile) {
      padding:0 15px;
    }
    @media screen and (min-width: $desktop-medium){
      padding: 0px 117px 0px 146px;

      .col-4 {
        padding: 0 20px 0 10px;
      }
      .col-8 {
        padding: 0 10px 0 0px;;
      }
    }

    @media screen and (min-width: $tablet-medium) and (max-width:  $desktop-medium ){
      padding: 0px 18px 0px 50px;
    }
  }

  h2 {
    @media screen and (max-width: $mobile ) { font-size: 36px;}
  }

  a {
    text-transform: uppercase;
    text-align: center;
    padding: 11px 22px;
    display: inline-block;
  }

  &__top {

    background: $textcolor;
    text-align: center;
    font-size: 14px;
    color: $white;
    padding: 20px 10px;
    @media screen and (min-width: $desktop-large){
      padding: 20px 40px;
    }
    .title {
      color: $color-39;
      font-size: 20px;
      font-family: $font-2;
      text-transform: uppercase;
      line-height: 1.3;
      padding-bottom: 2px;
      @media screen and (min-width: $desktop-large){
        font-size: 30px;
      }
    }

  }

  &__bottom {

    background: $white;
    font-size: 14px;
    padding: 27px;
    text-align: center;

    div {
      text-align: left;

      i {
        padding-right: 6px;
        display: table-cell;
        vertical-align: middle;
      }

      span {
        display: table-cell;
        vertical-align: middle;
      }
    }

    &__local {
      padding-top: 2px;

      i {

        padding-right: 8px !important;

        &:before {
          font-size: 17px;
          margin: 0;
          margin-left: 1px;
        }
      }
    }

    a {
      margin-top: 29px;
      display: inline-block;
      text-transform: uppercase;
      text-align: center;
      padding: 13px 48px;
    }
  }

  .select_items {

    margin-bottom: 20px;
    &>ul{
      list-style: none;
      &>li{
        background-color:  $textcolor;
        padding:0;
        width: 100%;
        color: $white;
        -webkit-appearance: none;
        appearance: none;
        -moz-appearance: none;
        cursor: pointer;
        font-family: $font-4;
        @media screen and (min-width: $tablet-medium) and (max-width: $tablet){
          padding-right: 20px;
        }
        span{
          font-size: 13px;
          font-family: $font-4;
          padding:0;
          text-align: left;
          height: 52px;
          line-height: 52px;
          text-transform: inherit;
          display: block;
          color:$white;
          position: relative;
          padding-left:20px;
          @media screen and (min-width: $tablet-medium) and (max-width: $tablet){
            padding-left: 10px;
            font-size: 12px;
          }
          &:after{
            content:"";
            background-image: url(../img/icon-down.png);
            background-repeat: no-repeat;
            background-position: 93% 50%;
            position: absolute;
            right:35px;
            top:50%;
            width: 12px;
            height:6px;
            transition: all .5s ease;
            -webkit-transform: translateY(-50%);
            -moz-transform: translateY(-50%);
            -ms-transform: translateY(-50%);
            -o-transform: translateY(-50%);
            transform: translateY(-50%);
            @media screen and (min-width: $tablet-medium) and (max-width: $tablet){
              right:-10px;
            }
          }
          &.active{
            &:after{
              transform:translateY(-50%) rotate(180deg);
            }
          }
        }
        ul{
          list-style: none;
          padding-left: 20px;
          margin-bottom: 0;
          border-top: 1px solid $white;
          display: none;
          li{
            margin:5px 0;
            position: relative;
            font-family: $font-4;
            label{
              margin-left: 10px;
              font-size: 13px;
              cursor: pointer;
              width: 100%;
              position: absolute;
            }
          }
        }
      }
    }
    select {
      height: 52px;
      background-color:  $textcolor;
      border: 2px solid $textcolor;
      width: 100%;
      color: $white;
      font-size: 13px;
      font-family: $font-4;
      background-image: url(../img/icon-down.png);
      background-repeat: no-repeat;
      background-position: 93% 50%;
      -webkit-appearance: none;
      appearance: none;
      -moz-appearance: none;
      cursor: pointer;
      padding-left: 20px;
    }

  }

  #offer__address {

    @media screen and (min-width: $tablet-medium){
      height: 660px;
      overflow-y: auto;
    }
    &::-webkit-scrollbar-track{
      -webkit-box-shadow: inset 0 0 6px #ffffff;
      background-color:#F5F5F5;
       border-radius: 10px;
    }
    &::-webkit-scrollbar{
      width: 5px;
      background-color: #ffffff42;
      border-radius: 10px;
    }
    &::-webkit-scrollbar-thumb{
      border-radius: 10px;
      background-color: #d9d9d9;
    }
    .mCS-dark.mCSB_scrollTools .mCSB_draggerRail {
      background-color:  $white;
      width: 5px;
    }

    .mCS-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar{
      background-color:  rgba(51, 51, 51, 0.25);
      width: 5px;
    }
  }
  .block__offer__address {
    background: $white;
    margin-bottom: 20px;
    padding: 26px 15px;
    display: table;
    width: 100%;
    &.disable{
      background: $color-28;
    }
    @media screen and (min-width: $tablet-medium){
      padding: 26px 32px;
    }

    &__left {
      display: block;
      width: 100%;
      font-size: 14px;

      @media screen and (min-width: $tablet-medium){
        display: table-cell;
        width: 60%;
      }

      .title {
        font-size: 16px;
        font-family: $font-4;
        padding-left: 5px;
        text-transform: uppercase;
        a{
          color:  $color-39;
          padding:0;
          text-align: left;
        }
      }

      span {
        display: block;
        font-family: $font-4;
        padding-left:5px;
      }

      i {
        padding-right: 10px;
        display: table-cell;
        vertical-align: middle;
      }

      .text_right_icon {
        display: table-cell;
        vertical-align: middle;
        padding-left: 0;
      }

      &__time {
        padding-top: 15px;
      }

      &__local {
        padding-top: 4px;

        i {     padding-right: 13px;

          &:before {
            font-size: 17px;
            margin: 0;
            margin-left:1px;
          }
        }
      }
    }

    &__right {
      display: block;
      width: 100%;
      vertical-align: middle;
      text-align: center;
      margin-top: 30px;

      @media screen and (min-width: $tablet-medium){
        display: table-cell;
        width: 60%;
        margin-top: 0px;
      }
    }
  }
}