.pagination{
  ul{
    list-style: none;
    text-align: center;
    margin-bottom: 0;
    li{
      display: inline-block;
      width: 23px;
      position: relative;
      a{
        color:$white;
        font-size: 13px;
        display: inline-block;
        i{
          font-size: 10px;
        }
        &.active{
          font-family: $font-2;
          font-size: 15px;
        }
      }
      &.prev-pagination{
        a{
          position: absolute;
          right:0;
          top:-15px;
        }
      }
      &.next-pagination{
        a{
          left:0;
          position: absolute;
          top:-15px;
        }
      }
    }
  }
}

.content__actus {

  .pagination{

    ul{

      li{

        a{

          color: $white;
        }
      }
    }
  }
}

#page_navigation_predicateur {
  text-align: center;

  a {
    color: $textcolor;
    padding: 0 7px;
    font-size: 15px;

    i {
      font-size: 10px;
    }

    &.active_page {

      font-family: $font-2;
    }
  }
}

/*Update Date 10/11/2017*/
.pagination{
  text-align: center;
  a{
    color: #333;
    font-size: 13px;
    font-size: 0.8125rem;
    display: inline-block;
    &.next{
      font-size:0;
      &:before{
        display: inline-block;
        font-family: tang;
        font-style: normal;
        font-weight: 400;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        content: '\e80e';
        font-size: 14px;
        line-height: 35px;        
      }      
    }
    &.previous{
      font-size:0;
      &:before{
        display: inline-block;
        font-family: tang;
        font-style: normal;
        font-weight: 400;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        content: '\e80d';
        font-size: 14px;
        line-height: 35px;         
      }  
    }    
  }
}