.produits-associes  { background: $white;    padding: 50px;

  h2 { font-size: 24px;
    @media screen and (min-width: $tablet-medium){
      font-size: 36px;
    }
  }

  .item { text-align: center; color: $color-11; font-size: 12px; @include transition(all .2s ease-in-out); cursor: pointer;  margin-top: 31px;

    span { font-weight: bold; display: block; font-size: 13px;  padding-bottom: 5px; }

    img { @include transition(all .2s ease-in-out);  margin-bottom: 20px; }

    &:hover {  color: $color-18;

      img { @include transform(scale(1.02)); @include transition(all .2s ease-in-out); }
    }
    a{
      color: $color-11;
      &:hover{
        color: $maincolor;
      }
      .image{
        height:250px;
        position: relative;
        margin-bottom:10px;
        &>img{
          max-height: 100%;
          position:absolute;
          top:50%;
          left:50%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }

  &-detail {
    background: $color-4 ;
    padding: 40px;
  }

}