/* Clients
----------------------------------------------------------------------------------------------------*/
.clients {
  background: $color-4;
  padding: 40px 15px 30px;
  @include clearfix;

  @media screen and (min-width: $tablet-medium) {
    padding: 35px 15px 60px;
  }

  .h4{
    margin: 0 0 15px 0;
    text-align: center;
    text-transform: uppercase;
    font-size: 20px;

    @media screen and (min-width: $tablet-medium) {
      margin: 0 0 40px 0;
    }
  }
}