.page__recette{

}
.content_recettes{
  .meta{
    .icon{
      display: inline-block;
      margin-right:15px;
      i{
        color: $white;
        font-size:15px;
      }
    }
  }
  .list_items{
    .items{
      .card{
        &__content{
          &:after{
            background: rgba($black, .4);
          }
        }
        &__body{
          @media screen and (max-width: $tablet) and (min-width: $tablet-medium){
            display: none;
          }
        }
      }
    }
  }
  .recettes-filter{
    margin-bottom: 39px;
  }
}
.content_are__raviolis{
  @media screen and (max-width: $tablet){
    width: 100%;
  }
}
.produits-recette{
  h2{
    @media screen and (max-width: $mobile){
      font-size: 19px;
    }
  }
  .item{
    a{
      color:$color-11;
      &:hover{
        color: $maincolor;
      }
      .image{
        height:250px;
        position: relative;
        margin-bottom:10px;
        &>img{
          max-height: 100%;
          position:absolute;
          top:50%;
          left:50%;
          transform: translate(-50%, -50%);
        }
      }
    }
  }
  .slick-arrow{
    z-index: 0;
    &:before{
      color: rgba($black, .6) !important;
    }
  }
  .slick-prev{
    left:-20px;
  }
  .slick-next{
    right:-20px;
  }
  @media screen and (min-width: $tablet){
    .slick-prev{
      left:-10px;
    }
    .slick-next{
      right:-10px;
    }
  }
}