/* Offer
----------------------------------------------------------------------------------------------------*/
.offer {
  position: relative;
  @include clearfix;

  [class^='col-'] {
    padding: 0;
    margin: 0;
  }

  .title__offer {
    position: absolute;
    width: 100%;
    top: 25px;
    text-align: center;
    z-index: 2;
    margin: 0;
    font-family: $font-1;
  }
  /* Offer || Home
  ----------------------------------------------------------------------------------------------------*/
  &__home {

    &.view{
      .item-1,
      .item-2 {
        @include transition-delay(.25s);
      }

      &-on {

        .item-1,
        .item-2 {
          @include transform (translate(0, 0));
        }
      }
    }

    .view{

    }
    [class^='col-'] {
      overflow: hidden;
    }

    .item-1 {
      @include transform (translate(-100%, 0));
    }

    .item-2 {
      @include transform (translate(100%, 0));
      a{
        div{
          color:$white;
        }
      }
    }
    a{
      position:relative;
      .title_home{
        margin-bottom:0;
        position:absolute;
        text-align: center;
        top:20px;
        left:0;
        right:0;
        z-index: 2;
        text-transform:uppercase;
        font-size: 30px;
        display: inline-block;
        margin:0 auto;
        color:$white;
        font-family: $font-2;
        @include transform(scale(1));
        @include transition(all .2s ease-in-out);

        @media screen and (min-width: $mobile-small){
          top:70px;
        }
        @media screen and (min-width: $tablet){
          font-size: 30px;
        }
        @media screen and (min-width: $desktop-medium){
          font-size: 70px;
          background: url(../img/bg-pro.png) no-repeat center top;
        }
      }
      div{
        display: inline-block;
        max-width: 300px;
        text-align: center;
        position:absolute;
        bottom:20px;
        left:50%;
        transform: translateX(-50%);
        @include transition(all .2s ease-in-out);
        @media screen and (max-width: $desktop-small){
          max-width: 90%;
          transform: translateX(0);
          text-align: center;
          width: 90%;
          left:5%;
        }
         @media screen and (max-width: 480px){
            font-size: 12px;
            bottom:10px;
         }
         p{
          margin-bottom:0;
         }
      }
    }
    img {
      width: 100%;
      height: 100%;
      display: block;
      @include transform(scale(1));
      @include transition(all .2s ease-in-out);
    }

    a {
      display: block;
      @include clearfix;

      @include hover {
        h2{
          @include transform(scale(1.02));
          @include transition(all .2s ease-in-out);
        }
        img {
          @include transform(scale(1.02));
          @include transition(all .2s ease-in-out);
        }
        span{
          @media screen and (min-width: $desktop-small){
            @include transform(translateX(-50%) scale(1.02));
          }
        }
      }
    }
  }

  /* Offer || Highlight
  ----------------------------------------------------------------------------------------------------*/
  &__highlight {
    padding: 0 30px;
    min-height: 320px;
    overflow: hidden;

    img {
      display: inline-block;
      margin: 0 auto;
      @include transform(scale(1));
      @include transition(all .2s ease-in-out);
    }

    @include hover {
      img {
        @include transform(scale(1.02));
        @include transition(all .2s ease-in-out);
      }
    }

    .h2 {
      color: $textcolor;
      position: absolute;
      top: 35px;
      left: 0;
      width: 100%;
      padding: 0 15px;
      z-index: 2;

      @media screen and (min-width: $tablet-medium) {
        top: 20px;
      }

      @media screen and (min-width: $desktop-small) {
        top: 50px;
      }
    }

    .img {
      position: absolute;
      left: 0;
      width: 100%;
      text-align: center;
      top: 54%;
      @include transform(translate(0px, -54%));
      //
      //@media screen and (min-width: $tablet-medium) {
      //  top: 50%;
      //  @include transform(translate(0px, -50%));
      //}
      //
      //@media screen and (min-width: $desktop-small) {
      //  top: 10%;
      //}
      //
      //@media screen and (min-width: $desktop-medium) {
      //  top: 0;
     // }
    }

    p {
      position: absolute;
      bottom: 25px;
      left: 0;
      padding: 0 15px;
      margin: 0;
      width: 100%;
      font-size: 13px;
      color: $textcolor;

      @media screen and (min-width: $tablet) {
        bottom: 45px;
      }
    }
  }
}