// sidebar

.page__produits {
  background: $color-4;
  position: relative;
  margin: 0;
}

.list-produits-category{
  width: 100%;
  position: relative;

  @media screen and (min-width: $tablet-medium)and (max-width: $tablet){
    width: 25%;
    float: left;
  }

  @media screen and (min-width: $tablet){
    width: 315px;
    float: left;
  }
}
.sidebar__produits, .list-produits-category {
  background: $color-4;
  //width: 100%;
  //position: relative;
  //
  //@media screen and (min-width: $tablet-medium)and (max-width: $tablet){
  //  width: 25%;
  //  float: left;
  //}
  //
  //@media screen and (min-width: $tablet){
  //  width: 315px;
  //  float: left;
  //}
  > ul {
    list-style:none;
    margin: 0;
    > li {
      padding:0 !important;
      border-bottom: 1px solid #fff;
      border-top: 1px solid #fff;
      position: relative;
      cursor: pointer;
      @include transition(all 0.3s ease);
      a{
        display: block !important;
        width: 100%;
        padding: 8px 10px 10px 30px !important;
      }
      @media only screen and (max-width: $mobile) {
        float: left;
        width: 33.33%;
        padding: 10px;
        text-align: center;
        min-height: 145px;
        border-right: 2px solid #fff;
        position: relative;
      }

      @media only screen and (min-width: $desktop-small) {
        padding: 8px 10px 10px 46px;
      }

      &:first-child {
        border-top: 2px solid $white;

        &:before {
          top:0px;
        }
      }

      i {
        &:before {
          font-size: 40px;
          margin: 0;
        }
      }

      span {
        display: inline-block;
        width: 25%;
        vertical-align: middle;
        padding-top: 5px;
        @media only screen and (max-width: $mobile) {
          display: block;
          width: 100%;
          text-align: center;
        }

        &.left-10 { padding-left: 10px;}
        &.left-5 { padding-left: 5px;}
        &.flat{
          padding-top: 0 !important;
          float:left;
          @media only screen and (max-width: $tablet) and (min-width: $tablet-medium){
            margin-left:25px;
            &+a{
              width:55%;
            }
          }
          img{
            height: 25px;
          }
        }
      }

      &>a {
        color: $color-17;
        font-family: $font-4;
        font-size: 14px;
        vertical-align: middle;

        @media only screen and (max-width: $mobile) {
          width: 100%;
          padding: 0px !important;
          position: absolute;
          left:0;
          right:0;
          top:50%;
          transform: translateY(-50%);
        }

        @media only screen and (min-width: $tablet-medium) and (max-width: 1023px) {
          padding-left: 10px;
        }
      }

      .border_color{
        position: absolute;
        left: 0;
        top: -2px;
        width: 13px;
        bottom:0;
        @include transition(all 0.3s ease);

        @media only screen and (max-width: $mobile) {
          display: none;
        }
      }
      @media screen and (min-width: $mobile) {
        &:hover{
          background: $white;

          @media only screen and (max-width: $mobile) {
            color: $white !important;

            a, i {
              color: $textcolor !important;
            }
          }

          > ul {
            display: block;

            @media only screen and (max-width: $mobile) {
              color: $white;

              a {
                color: $white;
              }
            }
          }

          .border_color {
            width: 24px;
          }
        }
      }
      &.active {
        background: $white;

        @media only screen and (max-width: $mobile) {
          color: $white !important;

          a, i {
            //color: $textcolor !important;
            color: $white !important;
          }
        }

        > ul {
          display: block;

          @media only screen and (max-width: $mobile) {
            color: $white;

            a {
              color: $white;
            }
          }
        }

        .border_color {
          width: 24px;
        }
      }
      &.active {
        > ul {
          display: none;
          @media screen and (min-width: $tablet-medium){
            display: block;
          }
        }
      }

      > ul {
        list-style: none;
        padding: 0;
        padding-left: 30%;
        display: none;
        @include transition(all 0.3s ease);

        @media only screen and (max-width: $mobile) {
          position: fixed;
          background: #fff;
          width: 100%;
          left: 0;
          right: 0;
          top:80px;
          z-index: 9;
          padding-left:0;
          height:434px;
          display: none;
        }
        li {
          &.parent{
            position: relative;
            @media screen and (min-width: $tablet-medium){
              display: none;
            }
            button{
              position: absolute;
              top:10px;
              right:20px;
              width: 25px;
              height:25px;
              span{
                background: $white;
                width: 20px;
              }
              span:nth-child(1){
                left:0px;
                top:10px;
              }
              span:nth-child(2){
                left:calc(50% - 12px);
                top:10px;
              }
            }
          }
          padding-bottom: 10px;

          @media only screen and (max-width: $mobile) {
            padding-bottom: 5px;
            padding-top: 5px;
            border-bottom: 1px solid $white;
            text-align: left;
            &:last-child{
              border-bottom: none;
            }
          }

          a {
            color: $color-17;
            font-family: $font-3;
            font-size: 14px;
            vertical-align: middle;
            @media screen and (max-width: $mobile){
              color:$white;
            }
          }

          &:last-child {
            padding-bottom: 0px;
          }

          &:hover{
            a {
              text-decoration: underline;
            }
          }
        }
      }

    }
  }
  &:after {
    content: "";
    display: block;
    clear: both;
  }

  &.sidebar-actua {
    > ul {
      >li {
        &:hover,&.active{
          background: $white;

          @media only screen and (max-width: $mobile) {
            color: $textcolor !important;

            a, i {
              color: $textcolor !important;
            }
          }
        }
      }
    }
  }
  &.sidebar-recettes{
    ul{
      li{
        @media screen and (max-width: $mobile){
          min-height: 45px;
        }
      }
    }
  }
  &.sidebar__recettes {
    >ul {
      li {
        @media only screen and (max-width: $mobile) {
          float: none;
          width: 100%;
          min-height: 0;
        }
      }
    }
  }
}

// content produits

.right-content__produits {
  width: 100%;
  position: relative;


  .pagination {
    display: table;
    width: 100%;
    ul {
      li {
        a {
          color: $textcolor;
        }
      }
    }
  }

  @media screen and (min-width: $tablet-medium)and (max-width: $tablet){
    width: 75%;
    float: left;
  }


  @media screen and (min-width: $tablet){
    @include calc(width, "100% - 315px");
    float: left;
    //margin-top: -33px;
  }

  //@media screen and (min-width: $tablet-medium){
  //  margin-top: -33px;
  //}

  .slide__produits {

    .slider__item {

      .sider__img {
        background-size: cover; min-height: 280px;

        @media screen and (min-width: $tablet-medium) {
          min-height: 462px;
        }

      }
      .description {
        position: absolute;
        top:49%;
        left: 15px;
        right: 15px;
        font-family: $font-2;
        font-size: 60px;
        text-align: center;
        color:  $white;
        text-transform: uppercase;
        @include transform(translate(0px, -50%));

        .text {
          position: relative;
          display: inline-block;
          text-shadow: 0px 0px 50px rgba(55, 65, 85, 0.6);
          background-color: rgba($maincolor, 0.6);
          padding: 29px 50px;

          @media only screen and (max-width: $mobile) {
            padding: 15px;
          }
        }

        @media screen and (max-width: $desktop-small){
          font-size: 36px;
          padding: 29px 15px;
        }

        @media screen and (max-width: $mobile){
          font-size: 25px;
          left: 0;
          right: 0;
        }
      }
    }
  }

  .produits-desctiption {
    padding: 37px 15px 60px;

    @media screen and (min-width: $tablet-medium){
      padding: 37px 53px 60px;
    }

    h2 {
      @media screen and (max-width: $desktop-small){
        font-size: 36px;
      }
    }

    .content {
      color: $white;
      text-align: justify;
      font-size: 13px;
      font-family: $font-3;
      padding-top: 8px;
      line-height: 19px;
    }
  }

  .list__slide__produits {

    .slides_col_produits {
      width: 100%;
      text-align: center;
      height: 350px;
      margin-bottom: 0;
      position: relative;

      @media screen and (min-width: $tablet-medium){
        float: left;
        width: 33.33%;
      }

      .title {
        font-size: 36px;
        font-family: $font-2;
        padding-top: 24px;
        position: absolute;
        left: 0;
        right: 0;

        @media screen and (max-width: $desktop-small){
          font-size: 24px;
        }
      }

      .slider__item {
        height: 100%;
        min-height: 350px;

        img {
          margin: 0 auto;
          position: absolute;
          right: 0;
          top: 52%;
          left: 0;
          @include transform(translate(0, -50%));
          max-height: 200px;

          @media screen and (max-width: $desktop-small){
            max-width: 200px;
          }

          @media only screen and (min-width: $tablet-medium) and (max-width: $tablet-big) {
            max-width: 125px;
          }
        }

        .description {
          font-size: 13px;
          color:  $white;
          position: absolute;
          bottom: 22px;
          left: 0;
          right: 0;

          a {
            color:  $white;
          }
        }
      }

      .slick-prev, .slick-next { top:52%; z-index: 999;  }

      .slick-prev { left: 10px; }

      .slick-next { right: 10px; }

      &.bg-gradient-gray {

        .description {
          color: $light_grey;
        }

        .slick-prev, .slick-next{

          &:before {
            color: $light_grey;
          }
        }

      }
    }
  }

  &:after {
    content: "";
    display: block;
    clear: both;
  }

  .list_sub_content_category {

    margin: 43px 0 0;
    display: table;
    width: 100%;
    .items {
      text-align: center;
      margin: 40px 0px;
      min-height: 183px;
      position: relative;
      @include transition(all .2s ease-in-out);
      .image{
        position: relative;
        height:125px;
      }
      img {
        max-width: 100%;
        position: absolute;
        right: 0;
        left: 0;
        top: 50%;
        max-height: 100%;
        transform: translateY(-50%);
        @include transition(all .2s ease-in-out);

      }

      &_sub_category {
        font-size: 14px;
        position: absolute;
        font-family: $font-3;
        left: 0;
        right: 0;
        top: 140px;
        color: $textcolor;
        padding: 0 20px;
      }

      &:hover {

        .items_sub_category  {
          text-decoration: underline;
        }

        img { top: 40%; }
      }
    }

  }
  .loading{
    position: absolute;
    left:50%;
    transform: translateX(-50%);
    z-index: 1;
    display: none;
  }
  &_category {
    padding: 30px 15px ;

    @media screen and (min-width: $tablet){
      padding: 39px 53px  ;
    }

    @media screen and (min-width: $tablet-medium) and (max-width: $tablet-big) {
      padding: 39px 30px 15px ;
    }

    .description_category {
      font-size: 13px;
      padding-top: 10px;
    }

    .category-filter, .recettes-filter {

      margin-top: 40px;
      input[type='submit']{
        display: none;
      }
      select {
        border: 2px solid $white;
        color: $white;
        font-size:13px;
        font-family: $font-2;
        height: 53px;
        padding: 0 20px;
        margin-right: 30px;
        display: inline-block;
        width: 40%;
        background-image: url(../img/icon-down-white.png) !important;
        background-repeat: no-repeat !important;
        background-position: 96% 50% !important;
        -webkit-appearance: none;
        appearance: none;
        -moz-appearance: none;
        cursor: pointer;
        // Removes default arrow from IE10+
        &::-ms-expand {
          display: none;
        }
        &:active,&:focus{
          background: transparent;
        }
        @media screen and (min-width: $desktop-medium){
          max-width: 315px;
        }
        @media screen and (max-width: $mobile){
          width: 100%;
          display: block;
          margin-bottom: 15px;
        }
      }
    }
  }
  &__child {
    padding: 30px 15px;

    @media screen and (min-width: $tablet){
      padding: 30px;
    }

    .description_category__child {
      font-size: 13px;
    }

    .items {
      position: relative;
      margin: 30px 0;

      &__content{

        padding-top: 20px;

        a {
          font-size: 15px;
          color: $white;
        }
      }

    }
  }
}
