/* Cooking
 ----------------------------------------------------------------------------------------------------*/
.cooking {
  background: $white;
  @include clearfix;

  @media screen and (min-width: $tablet-medium) {
    background: $color-3;
  }

  .h2 {
    text-align: center;
    color: $textcolor;
    text-transform: uppercase;
    position: relative;
    font-size: 36px;
    margin: 0;
    z-index: 2;

    @media screen and (min-width: $tablet-medium) {
      font-size: 70px;
    }
  }

  /* Cooking || Home
  ----------------------------------------------------------------------------------------------------*/
  &__home {
    position: relative;
    padding: 30px 0 0;
    @include clearfix;

    @media screen and (min-width: $tablet-medium) {
      padding: 45px 15px 15px;
      
    }
    @media screen and (max-width:767px){
      .slider__content{
        background: transparent;
        padding:0 !important;
        padding-left:5px !important;
      }
      .slider__img{
        width: 100%;
        padding-right:15px;
        padding-left:15px;
        @media screen and (max-width: $tablet-medium){
          padding-right:0;
          margin:50px auto 30px;
          padding-left:0;
          width: 400px;
          float:none !important;
        }
        @media screen and (max-width: $mobile-small){
          width: 80%;
        }
      }
      .slider__content{
        padding-left:20px !important;
        padding-right:15px !important;

      }
      .text__center{
        padding:20px 15px;
        background: $color-5;
        margin-bottom: 0;
        @media screen and (min-width: $tablet-medium) {
          background: none;
          padding:8px 15px;
        }
      }
      .slick-arrow{
        &.slick-prev{
          left:15px;
          @media screen and (max-width: $mobile-small){
            left:0;
          }
        }
        &.slick-next{
          right:15px;
          @media screen and (max-width: $mobile-small){
            right:5px;
          }
        }
      }
    }
    &:after{
      position: absolute;
      bottom: 0;
      background: $color-5;
      width: 100%;
      height: 120px;
      left: 0;
      z-index: 0;
      display: none;
      @media screen and (min-width: $tablet-medium) {
        display: none;
      }
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      max-width: 100%;
      width: 100%;
      height: 220px;
      background: $color-5;
      z-index: 0;

      @media screen and (min-width: $tablet-medium) {
        left: 50%;
        height: 100%;
        max-width: calc(100% / 3);
        transform: translateX(-50%);
      }
    }
  }
}
.col-6.pull-right{
  float:right;
  @media screen and (max-width: $tablet-medium){
    float:left;
  }
}