.faq__contact {

  display: table;
  width: 100%;

  &__accordion {
    padding: 30px 15px;
    width: 100% ;
    display: block;
    @media screen and (min-width: $tablet-medium){
        padding: 60px 50px 75px;
        width: 66% ;
        display: table-cell;
    }
    @media screen and (min-width: $tablet){
      padding: 60px 120px 75px 149px;
    }

  }
  &__info {
    text-align: center;
    vertical-align: middle;
    width: 100% ;
    display: block;
    padding: 30px;
    @media screen and (min-width: $tablet-medium){
      width: 34%;
      display: table-cell;
      height: 455px;
    }
    h1{
      text:{
        transform: uppercase;
      }
    }
  }

  .block {
    margin-bottom: 2px;
    .accordion {
      //max-height: 0;
      //opacity: 0;
      display: none;
      @include transition(all .5s ease);
      .ui-accordion-content{
        transition: all .5s ease;
      }
      /*.ui-accordion-content-active{
        display: block !important;
        height:auto !important;
      }*/
    }

    &.active {
      //opacity: 1;
      //max-height: 100%;

      .accordion {
        display: block;
      }

    }
  }

  .toogle__accordion {
    background: $color-17;
    color: $white;
    font-size: 13px;
    font-family: $font-4;
    padding: 18px;
    position: relative;
    cursor: pointer;
    margin-bottom: 0;
    &.close {
      &:after {
        content: '';
        @include transform(rotate(-180deg));
      }
    }

    &:after {
      content: '';
      background:url("../img/icon-down.png") no-repeat;
      position: absolute;
      top: 50%;
      @include transform(translate(0px, -50%));
      right: 20px;
      width: 12px;
      height: 6px
    }
  }

  .ui-accordion {
    .ui-accordion-header {
      background: $white;
       border: none;
      font-size: 16px;
      font-family: $font-2;
      color: $color-17;
      border-top: 1px solid $color-3;
      padding: 14px 0;

      .accordion_title {
        display: block;
        max-width: 90%;
      }

      &:first-child {
        border-top: none;
      }

      .ui-accordion-header-icon {
        background:url("../img/icon-plust.png") no-repeat;
        width: 12px;
        height: 12px;
        position: absolute;
        right: 20px;
        top: 50%;
        @include transform(translate(0px, -50%));
      }

      &.ui-state-active {
        .ui-accordion-header-icon {
          background:url("../img/icon-minus.png") no-repeat;
          height: 2px;
          top: 28px;
        }
      }

      &.none {

        .ui-accordion-header-icon {
          background: none;
        }
      }
    }

    .ui-accordion-content {
      border: none;
      padding: 0px;
      font-size: 13px;
      max-width: 88%;
      height:0;
      opacity: 0;
      &.none {
        height: auto !important;
        opacity: 1;
      }
    }
  }
}