.content-histoire {
  position: relative;
}
.languages__histoire{
  position: absolute;
  top: 4px;
  right: 30px;
  z-index: 9;
  @media screen and (max-width: $mobile){
    position: static;
    padding:10px 0;
    background: $color-3;
  }
  &__fr {

    a {
      background: url("../img/flag-france.png");
    }

  }
  &__en {
    a {
      background: url("../img/flag-united-kingdom.png");
    }

  }
  &__ch {
    a {
      background: url("../img/china.png");
    }

  }

  ul {
    list-style: none;
    margin:0;
    @media screen and (max-width: $mobile){
      text-align: center;
    }
    li {

      float: left;
      @media screen and (max-width: $mobile){
        float:none;
        display: inline-block;
      }
      a {
         text-indent: -9999px;
         color: transparent;
         background-repeat: no-repeat !important;
         opacity: 0.6;
          padding:0;
        &.active {
          opacity: 1;
        }
      }

    }
  }
}

.en,.ch,.fr {
  display: none;

  &.active {
    display: block;
  }
}

.histoire {
  background: $color-3;
  text-align: center;
  position: relative;
  z-index: 1;
  [class^="col-"], [class*=" col-"] {
    margin-bottom: 0;
  }


  h2 {
    padding-top: 56px;
    z-index: 99;
    position: relative;
    @media screen and (max-width: $mobile) {
      font-size: 36px;
    }
  }

  &:after {
    background: url('../img/dot.png') repeat-y;
    content: '';
    height: 100%;
    position: absolute;
    top: 0;
    width: 2px;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 1;
  }

  .content-are-histoire {
    position: relative;
    margin: 0 auto;
    z-index: 99;
    overflow: hidden;
    max-width: 990px;
    padding: 0 15px;

    @media screen and (min-width: $tablet-medium) {
      padding: 0 30px 40px;
    }

    .items {
      position: relative;

      .year {
        font-weight: 900;
        font-size: 70px;
        line-height: 30px;
        margin-top: 53px;
        margin-bottom: 30px;
        font-family: $font-2;

        &.year01 {
          @media screen and (min-width: $tablet-medium) {
            margin-bottom: 100px;
          }

        }
      }

      .image {
        z-index: 99;
        padding: 0;

        &.image-left {
          padding-left: 45px;

          &.has_shadow_image__left {

            img {
              @include box-shadow(0, 15px, 50px, 0, rgba(#3e3e3e, .5));
            }
          }
        }

        &.right {
          padding-left: 60px;

          &.has_shadow_image__right {
            img {
              @include box-shadow(0, 15px, 50px, 0, rgba(#3e3e3e, .5));
            }
          }
        }

        @media screen and (max-width: $mobile) {
          text-align: center;
          padding-left: 0 !important;
        }
      }

      .content {
        text-align: left;
        word-spacing: 3px;
        font-size: 13px;
        color: $color-11;
        background: $white;
        z-index: 1;
        padding: 58px 70px;
        position: absolute;
        line-height: 19px;
        &.content-left {
          left: 15px;
          top: 91px;
          @include box-shadow(0, 15px, 50px, 0, rgba(#3e3e3e, .1));
          @media screen and (min-width: $desktop-small) {
            width: 402px;
            left: 5px;
          }
          @media only screen and (min-width: 768px) and (max-width: 1023px) {
            width: 42%;
          }
        }

        &.content-right {
          right: 20px;
          bottom: -20%;
          @include box-shadow(0, 15px, 50px, 0, rgba(#3e3e3e, .1));

          @media screen and (min-width: $desktop-small) {
            width: 332px;
            right: 30px;
          }
        }

        @media screen and (max-width: $mobile) {
          position: relative;
          left: 0 !important;
          width: 100% !important;
          right: 0;
          top: 0 !important;
          bottom: 0 !important;
          padding: 25px 20px;
          height: auto !important;

        }

        @media only screen and (min-width: 768px) and (max-width: 1023px) {
          padding: 40px;
        }
      }

      &:nth-of-type(2n) {

        @media only screen and (min-width: $tablet-medium) {
          margin-top: 137px;
        }
      }

      &:after {
        content: "";
        display: block;
        clear: both;
      }

    }

    .mCSB_inside > .mCSB_container {
      margin-left: 15px;
      margin-right: 20px;

      @media screen and (min-width: $tablet-medium) {
        padding-right: 45px;
        margin-right: 0;
        margin-left: 0px;
      }
    }

    .mCSB_scrollTools .mCSB_draggerContainer {
      top: 90px;
      height: 714px;
    }

    .mCSB_scrollTools .mCSB_draggerRail {
      width: 5px;
      background-color: $white;
    }
    .mCS-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
      background-color: #d2d2d2;
      width: 5px;
    }

  }

}