.page__category__recettes {

  @media screen and (max-width: $mobile){
    h2 {
      font-size: 36px;
    }
  }

  .right-content__produits {
    padding: 50px 0;
  }
}

.recettes__category {

  padding: 0 15px;
  @media screen and (min-width: $tablet){
    padding: 32px 55px 32px 90px;
  }

  @media screen and (min-width: $tablet-medium)and (max-width: $tablet){
    padding: 32px 50px;
  }
  .col-6,  .col-7 {

    @media screen and (min-width: $tablet-medium){
      display: inline-block;
      vertical-align: middle;
      float: none;
      margin-bottom: 0;
    }
  }

  .recette__content {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
    @media screen and (min-width: $desktop-small){
      width: calc(100% - 370px);
    }
    @media screen and (min-width: $tablet-medium)and (max-width: $desktop-small){
      width: 50%;
    }
  }

  &__block {

    position: relative;

    margin-bottom: 47px;

    @media screen and (min-width: $tablet-medium){
      display: table;
      width: 100%;
    }

    &__content {
      float: left;
      padding: 30px 15px ;

      @media screen and (min-width: $tablet){
        padding: 45px 40px 48px;
      }

      @media screen and (min-width: $tablet-medium)and (max-width: $tablet){
        padding: 35px 25px;
      }

      @media screen and (min-width: $tablet-medium){
        max-width: 380px;
        float: right;
        margin-right: -35px;
        position: relative;
      }
      background: $white;
      -webkit-box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.2);
      -moz-box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.2);
      -o-box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.2);
      box-shadow: 0 0 50px 0 rgba(0, 0, 0, 0.2);

      &.block-left {
        float: left;
        margin-right: 0;
        @media screen and (min-width: $tablet-medium){
          margin-left: -35px;
        }
      }

      .title {

        padding-bottom: 10px;

        a {
          color: $textcolor;
          text-transform: uppercase;
          font-family: $font-2;
          font-size: 24px;
          line-height: 0.8;
        }
      }

      .time {
        display: inline-block;
        text-align: left;
        font-size: 12px;

        i {
          color: $color-16;
          padding-right: 3px;

          &:before {
            margin: 0;
            font-size: 18px;
          }
        }

        &.time-last {
          margin-left: 16px;

          i {

            padding-right: 12px;

            &:before {
              font-size: 15px !important;
            }
          }
        }
      }

      .content {
        display: block;
        font-size: 12px;
        padding-top: 25px;
        line-height: 1.8;
      }
    }

    &__img {

      &.block-left-img {
        padding: 0 15px;

        @media screen and (min-width: $tablet-medium){
          display: inline-block;
          width: 370px;
          vertical-align: middle;
          z-index: 99;
          position: relative;
        }

        @media screen and (min-width: $tablet-medium)and (max-width: $desktop-small){
          width: 50%;
        }
      }

      &.block-img-right {

        img {
          text-align: right;
          display: inherit;
        }

      }

      img {
        -webkit-box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.2);
        -o-box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.2);
        box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.2);
        margin: 0;
      }

      @media screen and (max-width: $mobile){
        text-align: center;

        img {
          display: inline-block;
        }
      }
    }

    &.center {
      margin: 0 80px;
    }

    &.left {
    }

    &:after {
      content: "";
      display: block;
      clear: both;
    }
  }
}
