/* Slider
----------------------------------------------------------------------------------------------------*/
.slider {
  overflow: hidden;
  position: relative;
  @include clearfix;

  .slick-list {
    max-width: 100%;
    max-height: 100%;
  }

  &.slick-dotted.slick-slider {
    margin: 0;
  }

  .slick-dots {
    bottom: 10px;

    li {
      margin: 0;
      width: 22px;

      &.slick-active {
        button {
          &:before {
            background: $color-12;
            height: 12px;
            width: 12px;
            margin-top: 4px;
          }
        }
      }

      button {
        padding: 0;
        text-align: center;

        &:before {
          content: '';
          background: $white;
          padding: 0;
          height: 8px;
          width: 8px;
          margin: 6px 0 0 6px;
          @include border-radius(50%);
          @include opacity(1);
        }
      }
    }
  }

  .meta {
    @include clearfix;

    p {
      margin: 0;
      float: left;
    }
  }

  /* Slider || Item
  ----------------------------------------------------------------------------------------------------*/
  &__item {
    position: relative;

    .slider__cooking & {

      @media screen and (min-width: $tablet-medium) {
        margin: 35px;
      }
    }
    .link{
      position:absolute;
      left:0;
      right:0;
      top:0;
      bottom:0;
      z-index: 30;
      font-size: 0;
    }
  }

  /* Slider || Content
  ----------------------------------------------------------------------------------------------------*/
  &__content {
    background: $white;

    .slider__cooking & {
      padding: 15px !important;
      margin: 40px 0 0 !important;

      @media screen and (min-width: $tablet-medium) {
        padding: 35px !important;
        height: 260px;
        margin: 15px 0 0 !important;
        @include box-shadow(0, 0, 50px, 0, rgba($black, .2));
      }

      @media screen and (min-width: $desktop-small) {
        padding: 40px 40px 15px 40px !important;
        margin: 40px 0 0 !important;
      }
    }
  }

  /* Slider || Img
  ----------------------------------------------------------------------------------------------------*/
  &__img {
    @include clearfix;
    .slider__cooking & {
      @media screen and (min-width: $tablet-medium) {
        @include box-shadow(0, 0, 30px, 0, rgba($black, .2));
      }
    }

    img {
      display: block;
      margin: 0 auto;
      width: 100%;
    }
  }

  /* Slider || Home
  ----------------------------------------------------------------------------------------------------*/
  &__home {
    @media screen and (max-width: $mobile){
      max-height: 380px;
    }

    @media screen and (min-width: $tablet-medium) {
      font-size: 70px;
      /*max-height: 680px;*/
    }

    h1, p {
      color: $white;
      margin: 0;
      text-transform: uppercase;
      line-height: normal;
      span{
        display: block;
      }
    }
    &-mobile{
      .slider__item{
        @media screen and (min-width:768px){
          background-size:cover !important;
          @media screen and (max-width: $mobile){
            visibility: hidden;
          }
          /*.slider__img{
            visibility: hidden;
          }*/
        }
        @media screen and (max-width:767px){
          height: 380px;
          .slider__img{
            display: none;
          }
        }
      }
    }
  }

  /* Slider || Body
  ----------------------------------------------------------------------------------------------------*/
  &__body {
    @include clearfix;
    p {
      margin: 0;

      .slider__cooking & {
        font-size: 13px;
        line-height: 20px;
        font-family: $font-3;
      }
    }

    .slider__home & {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      margin: 0;
      text-align: center;
      align-items: center;
      > div {
        padding: 15px;
        background: rgba($maincolor, .6);
        max-width: 70%;
        @media screen and (min-width: $tablet-medium) {
          padding: 30px;
        }
        .title__large{
          @media screen and (max-width: $mobile){
            font-size: 25px;
          }
        }
      }
    }
  }

  /* Slider || Card
  ----------------------------------------------------------------------------------------------------*/
  &__card {
    max-width: 1270px;
    margin: 0 auto;
    padding: 5px;

    @media screen and (min-width: $tablet-medium) {
      padding: 5px 30px;
    }

    @media screen and (min-width: $desktop-medium) {
      padding: 5px 100px;
    }

    h3,
    p {
      color: $white;
    }
    .card__item{
      a{
        @media screen and (max-width: $mobile){
          width: 400px;
          margin:0 auto;
          position: relative;
        }
        @media screen and (max-width: $mobile-small){
          width: 100%;
          margin:0 auto;
          position: relative;
        }
      }
    }
  }

  /* Slider || Cooking
  ----------------------------------------------------------------------------------------------------*/
  &__cooking {
    min-height: 340px;
    max-width: 960px;
    margin: -10px auto 0;
    position: relative;
    z-index: 1;
    @include clearfix;

    @media screen and (min-width: $tablet-medium) {
      padding: 0 15px;
      margin: -20px auto 0;
    }

    @media screen and (min-width: $desktop-small) {
      padding: 0 75px;
      margin: -60px auto 0;
    }

    .col-6 {
      padding: 0;
      margin: 0;
    }

    img {
      display: block;
      width: 100%;
      @media screen and (max-width: $mobile-small){
        height:auto;
      }
    }

    .h3 {
      text-transform: uppercase;
      font-size: 23px;
      line-height: 25px;
      margin: 0 0 15px 0;
      a{
        color: $textcolor;
      }
      @media screen and (max-width: $mobile){
        text-align: center;
      }
    }

    .meta {
      margin-bottom: 25px;

      p {
        font-size: 13px;
        font-family: $font-1;
        margin: 0 25px 0 0;
      }
    }

    .text__center {
      float: left;
      margin: 0px 0 5px;
      width: 100%;
      position: relative;
      z-index: 2;

      @media screen and (min-width: $tablet-medium) {
        margin: 40px 0 0;
      }
    }

    .slick-prev, .slick-next {
        display: block;
        &:before {
          color: $color-13;
        }
    }
  }

  /* Slider || Client
  ----------------------------------------------------------------------------------------------------*/
  &__client {
    max-width: 2200px;
    margin: 0 auto;
    padding: 0 30px;

    @media screen and (min-width: $desktop-small) {
      padding: 0;
    }

    img {
      margin: 0 auto;
    }

    .slick-prev, .slick-next {
      &:before {
        color: $color-13;
      }
    }
  }
}

.slick-prev,
.slick-next {
  height: 26px;

  &:before {
    content: '\e80d';
    font-family: tang;
    font-size: 26px;
  }
}

.slick-prev { z-index: 999;
  left: 0;
  &:before {
    content: '\e80d';
  }
}

.slick-next {
  right: 0;
  &:before {
    content: '\e80e';
  }
}