/* Utility
----------------------------------------------------------------------------------------------------*/
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

img,
iframe {
  max-width: 100%;
}

.hide {
  display: none;
}

.show {
  display: block;
}

/* Hidden
----------------------------------------------------------------------------------------------------*/
.hidden {

  &-xs {
    @media screen and (max-width: 767px) {
      display: none !important;
    }
  }

  &-md {
    @media screen and (max-width: 1023px) {
      display: none !important;
    }
  }
}

/* Visible
----------------------------------------------------------------------------------------------------*/
.visible {

  &-xs {
    display: block;

    @media screen and (min-width: 767px) {
      display: none !important;
    }
  }
}

/* Display
----------------------------------------------------------------------------------------------------*/
.d {

  &b {
    display: block;
  }
}