/* Business
 ----------------------------------------------------------------------------------------------------*/
.business {
  @media screen and (min-width: $tablet-medium) {
    @include flex();
  }
  [class^='col-'] {
    margin-bottom: 0 !important;
    padding: 35px 15px 45px;
    text-align: center;

    @media screen and (min-width: $tablet-medium) {
      padding: 30px 15px;
    }

    @media screen and (min-width: $desktop-small) {
      padding: 50px 30px;
    }
  }

  .h2 {
    margin: 0;
    text-transform: uppercase;
    font-size: 36px;
    line-height: 36px;

    @media screen and (min-width: $tablet-medium) {
      font-size: 32px;
      line-height: 32px;
    }

    @media screen and (min-width: $desktop-small) {
      font-size: 40px;
      line-height: 40px;
    }

    @media screen and (min-width: $desktop-medium) {
      font-size: 60px;
      line-height: 48px;
    }
  }
  .slides{
    margin:0;
    @media screen and (min-width: $tablet) and (max-width: $desktop-medium-l){
      margin:-35px 0 0 0;
    }
    @media screen and (min-width: $desktop-large){
      margin:-5px 0 0 0;
    }
    a{
      .img{
        position: static;
        transform: translate(0);
        @media screen and (max-width: $mobile){
          padding:25px 0;
        }
      }
      p{
        position: static;
        @media screen and (min-width: $tablet){
          margin-top: 15px;
        }
      }
    }
    button{
      &:before, &:after{
        color: $textcolor;
      }
    }
  }
  .facebook-review-box{
    @include bg-gradient(left, $color-43, $color-44);
    .facebook-review{
      @include clearfix();
      .wp-facebook-place{
        display: none;
      }
      .wp-facebook-content-inner{
        .wp-facebook-reviews{
          margin: 40px auto;
          width: 80%;
          .wp-facebook-review{
            background: $white;
            position: relative;
            padding:25px 10px;
            margin:0 auto;
            @include clearfix();
            text-align: left;
            height:210px;
            @media screen and (min-width: $tablet-medium){
              height:250px;
            }
            @media screen and (min-width: $tablet-big){
              height:180px;
            }
            @media screen and (min-width: $desktop-medium){
              height:210px;
            }
            @media screen and (min-width: $desktop-large){
              height:200px;
            }
            @media screen and (min-width: $desktop-large){
              padding:25px;
            }
            .wp-facebook-top{
              margin-bottom: 10px;
              margin-top: 0;
              font-size: 13px;
              font-weight: 400;
              overflow: hidden;
              &:after{
                @include clearfix;
              }
              img{
                float:left;
                margin-right: 10px;
              }
              .info{
                float:left;
                padding-top: 8px;
                p{
                  margin:0;
                  &.name{
                    font-weight: 700;
                  }
                }
              }
              a{
                color: $color-45;
                display: block;
              }
            }
            .wp-facebook-time{
              display: block;
              font-size: 12px;
            }
            .wp-facebook-text{
              font-size: 14px;
            }
            .wp-facebook-stars{
              @media screen and (min-width: $desktop-large){
                position: absolute;
                right:20px;
                top:38px;
              }
              @media screen and (max-width: $mobile) and (min-width: $mobile-small){
                position: absolute;
                right:20px;
                top:38px;
              }
              display: inline-block;
              color: $white;
              background: $color-46;
              font-size: 12px;
              padding: 2px 5px;
              border-radius: 10px;
              line-height: 0;
              margin-bottom: 3px;
              text-align: center;
              .wp-star{
                margin-left: 3px;
                svg{
                  path{
                    fill: #f57900;
                  }
                }
              }
            }
          }
          .slick-prev{
            left:-45px;
            @media screen and (max-width: $mobile-small){
              left:-40px;
            }
          }
          .slick-next{
            right:-35px;
          }
        }
      }
    }
    .button{
      text-align: center;
      text-transform: uppercase;
      color: $white;
      font-size: 13px;
      a{
        display: inline-block;
        padding:15px 30px;
        border:2px solid $white;
        color: $white;
        transition: all .2s ease;
        &:hover{
          background: $white;
          color: $color-44;
        }
      }
    }
  }
  .offer__highlight{
    @media screen and (max-width: $desktop-medium){
      .img {
        padding: 20px;
      }
    }
    @media screen and (max-width: $tablet-big){
      .img {
        top:50%;
      }
    }
    @media screen and (max-width: $mobile){
      padding-bottom:0px !important;
      .img{
        position:relative;
        padding: 50px 0;
        height:250px;
        top:140px;
        img{
          max-height: 100%;
        }
      }
    }
  }
  .wpcf7-not-valid-tip{
    color: $white;
  }
}

/* Recrutement
 ----------------------------------------------------------------------------------------------------*/
.recrutement {
  background: url('../img/recrutement.jpg') center center no-repeat;
  background-size: cover;
  .button__outline{
    @media screen and (min-width: $tablet-medium){
      position: absolute;
      bottom: 50px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  /* Recrutement || List
  ----------------------------------------------------------------------------------------------------*/
  &__list {
    margin: 30px 0;
    p{
      text-align: center;
      color:$white;
    }
    ul {
      margin: 0;
    }

    li {
      display: block;
      list-style: none;
      margin-bottom: 15px;
      @include clearfix;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      float: left;
      text-align: left;
      color: $white;
      font-family: $font-4;
      text-transform: uppercase;
      font-size: 13px;
      line-height: 15px;
      position: relative;
      .job-manager-applications-applied-notice{
        display: none;
      }
      @media screen and (min-width: $tablet-medium) {
        font-size: 14px;
        line-height: normal;
      }

      @include hover {
        .first {
          &:before {
            width: 100%;
            @include transition(all .2s ease-in-out);
          }
        }
      }

      .first {
        position: relative;

        &:before {
          content: '';
          height: 2px;
          width: 0;
          background: $white;
          display: block;
          position: absolute;
          bottom: -2px;
          left: 0;
          @include transition(all .2s ease-in-out);
        }
      }

      .last {
        font-family: $font-3;
        text-transform: initial;
      }
    }
  }
}